import React from 'react';

class ArjunaneFlutterModel extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`dart arjunane create:model mencoba`,

`import '../../system/arjunane.dart';

class MencobaModel extends Model {

  MencobaModel provider() => updateProvider<MencobaModel>();

  void exampleMethod(Controller controller) {

    // var con = controller as AsiyapController;
    // con.title = "Mencoba saja";
    notifyListeners();
  }

}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "model" ? "block" : "none"}}>
                    <h1>Model class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span><strong>Models</strong> are optionally available for those who want to use a more traditional MVC approach.</span>
                        </div>
                        <div className="col-mx">
                            Models are used to work with data on your server. Also, Models use Flutter's "<a target="_blank" href="https://pub.dev/packages/provider">Provider</a>" package, so Models can work with your UI at the same time
                        </div>
                        <div className="col-mx">
                            <span>Use command below to create a Model:</span>
                        </div>
                        
                        <pre className="syntax-global">{this.syntax[0]}</pre>

                        <div className="col-mx">
                            This command will generate a controller at app/model/mencoba_model.dart. The model will contain a method for each of the available resource operations:
                        </div>
                        
                        <pre className="syntax">{this.syntax[1]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterModel;