import React from 'react';

class ArjunaneUnityInsertGameObject extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`public class Example : MonoBehaviour
{
    public GameObject prefabGameObject;
    public GameObject[] prefabGameObjects = new GameObject[10];

    void Start()
    {
        var position = new Vector3(0,0,0);
        ar.InsertGameObject(prefabGameObject, position, Quaternion.identity);

        ar.Get(prefabGameObjects).ForEach( (go, i) => {
            ar.InsertGameObject(go, position, Quaternion.identity);
        });
    }
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "insert-gameobject" ? "block" : "none"}}>
                   <h1>Insert Game Object(s)</h1>
                    <div className="col-mx">
                        <div className="col-mx">
                            <span>Insert or add your <strong>prefab(s)</strong></span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityInsertGameObject;