import React from "react";

class NavLeft extends React.Component {

    constructor(props) {
        super(props);
        var setActive = this.props.setActive;
        this.state = {
            activeClass : setActive !== 'profile' && setActive !== 'portfolio' && setActive !== 'docs' ? '' : setActive
        };

        this.isOnClicked = false;
    }
    changePage(e, page) {
        e.preventDefault();
        var className = e.target.className;
        if(!this.isOnClicked && className.indexOf("active") === -1) {
            this.isOnClicked = true;
            this.setState({
                activeClass: page
            }, () => {
                this.props.onClicked(e, this);
            });
            setTimeout( () => {
                this.isOnClicked = false;
            }, 500);
        }
        
    }

    render() {
        var isActive = this.state.activeClass;
        return (
            <ul>
                <li>
                    <a className={ "nav-list" + (isActive == "" ? " active" : "")} onClick={(e) => this.changePage(e, "")} href="/">WELCOME</a>
                </li>
                <li>
                    <a className={ "nav-list" + (isActive == "profile" ? " active" : "")} onClick={(e) => this.changePage(e, "profile")} href="/profile">PROFILE</a>
                </li>
                <li>
                    <a className={ "nav-list" + (isActive == "portfolio" ? " active" : "")} onClick={(e) => this.changePage(e, "portfolio")} href="/portfolio">PORTFOLIO</a>
                </li>
                <li>
                    <a className={ "nav-list" + (isActive == "docs" ? " active" : "")} onClick={(e) => this.changePage(e, "docs")} href="/docs">DOCUMENTATIONS</a>
                </li>
            </ul>
        );
    }
}

export default NavLeft;