import React from 'react';

class ArjunaneTableV1DocsSearch extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`search : 
[
    { index: 0, type: "input", placeholder: "Search ID..."}, // If you hide this index, this is will not show at all.
    { index: 1, type: "input", placeholder: "Search Name..."},
    { index: 2, type: "input", placeholder: "Search Grade..."},
    { index: 3, type: "select", value: [
                                    ["", "All"], 
                                    [1, "Pass"],
                                    [0, "Not Pass"],
                                ]
    }
]
/*
group_by Example :
Let's say, you have some data
*/
Code | Country
1  | Kajen
2  | Bojong
2  | Bojong
3  | Wiradesa
4  | Karanganyar

search : 
[
    index       : 1, 
    type        : "select", 
    group_by    : 
    { 
        key : 0, 
        value : 1, 
        default : ["", "All"] 
    }
]

// Output Search:
<option value="">All</option>
<option value="1">Kajen</option>
<option value="2">Bojong</option>
<option value="3">Wiradesa</option>
<option value="4">Karanganyar</option>

// OR

search : 
[
    index       : 1, 
    type        : "select", 
    group_by    : 
    { 
        key : 1, 
        value : 1, 
        default : ["", "All"] 
    }
]

// Output Search:
<option value="">All</option>
<option value="Kajen">Kajen</option>
<option value="Bojong">Bojong</option>
<option value="Wiradesa">Wiradesa</option>
<option value="Karanganyar">Karanganyar</option>`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "search" ? "block" : "none"}}>
                    <h1>Tutorials : 3. Search</h1>
                    <div className="col-mx">
                        <ul>
                            <li>search : 
                                <ul>
                                    <li>index</li>
                                    <li>type : STRING ("input" <strong>OR</strong> "select")</li>
                                    <li>placeholder : STRING (for <strong>"input"</strong> type)</li>
                                    <li>value : ARRAY =&gt; STRING, STRING (for <strong>"select"</strong> type)</li>
                                    <li>group_by : (for <strong>"select"</strong> type)
                                        <ul>
                                            <li>key : INT</li>
                                            <li>value : INT</li>
                                            <li>default: ARRAY =&gt; STRING, STRING</li>
                                        </ul>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                    <div className="col-mx">
                        <span>Index start from zero (<strong>0</strong>) even if you hide the index of 0</span>
                    </div>
                    <div className="col-mx bg-w2 p-x">
                        <pre className="syntax">{this.syntax[0]}</pre> 
                    </div>
                </div>;
    }
}

export default ArjunaneTableV1DocsSearch;