import React from 'react';

class ArjunaneXamarinValidationOnInput extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`valid.OnInput("email", (error) => {
    if(error != null)
    {
        // Handle the error
    }
    else
    {
        // Nothing error
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "on-input" ? "block" : "none"}}>
                   <h3>On Input</h3>
                    <div className="col-mx">
                        <span><strong>OnInput</strong> is raised whenever the Text of the <strong>Entry</strong> or <strong>Editor</strong> changes.</span>
                    </div>
                    <pre className="syntax">{this.syntax[0]}</pre>
                    <div className="col-mx">
                        <span>The above method take one parameter and action:</span>
                        <ul>
                            <li>The field name or key that you've set in <strong>SetRules</strong>.</li>
                            <li>Get the error from your key.</li>
                        </ul>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinValidationOnInput;