import React from 'react';

class ArjunaneTableV2DocsSimpleInit extends React.Component {
    constructor(props) {
        super(props);
        this.isDone = false;
        this.syntax = [
            `0 : { name : "Sudrun", class : "XI", grade : 90}
1 : { name : "Caridi", class : "X", grade : 69}`,

            `var arTable = arTable.init("#table", {

                url : "json.json",
                title: "Data Orang",
                thead: ["ID", "name", "Grade", "Passing", "Sex", "Birth", "Place"]

            });`,

            `var json_table = 
            [

                { "id" : 1, "name" : "Name number 1", "grade" : 70, "passing" : 1, "sex" : "Female", "birth" : "2020-04-18", "place" : "Kedungwuni"},
                { "id" : 2, "name" : "Name number 2", "grade" : 10, "passing" : 0, "sex" : "Male", "birth" : "2020-04-17", "place" : "Linggo Asri"},
                { "id" : 3, "name" : "Name number 3", "grade" : 80, "passing" : 1, "sex" : "Female", "birth" : "2020-04-16", "place" : "Bojong"},
                { "id" : 4, "name" : "Name number 4", "grade" : 30, "passing" : 0, "sex" : "Male", "birth" : "2020-04-15", "place" : "Bojong"},
                { "id" : 5, "name" : "Name number 5", "grade" : 100, "passing" : 1, "sex" : "Female", "birth" : "2020-04-14", "place" : "Kedungwuni"},
                { "id" : 6, "name" : "Name number 6", "grade" : 30, "passing" : 0, "sex" : "Male", "birth" : "2020-04-13", "place" : "Kajen"},
                { "id" : 7, "name" : "Name number 7", "grade" : 100, "passing" : 1, "sex" : "Female", "birth" : "2020-04-12", "place" : "Kedungwuni"},
                { "id" : 8, "name" : "Name number 8", "grade" : 80, "passing" : 1, "sex" : "Male", "birth" : "2020-04-11", "place" : "Bojong"},
                { "id" : 9, "name" : "Name number 9", "grade" : 10, "passing" : 0, "sex" : "Female", "birth" : "2020-04-10", "place" : "Kajen"},
                { "id" : 10, "name" : "Name number 10", "grade" : 20, "passing" : 0, "sex" : "Male", "birth" : "2020-04-09", "place" : "Bojong"},
                { "id" : 11, "name" : "Name number 11", "grade" : 50, "passing" : 0, "sex" : "Male", "birth" : "2020-04-09", "place" : "Pekajangan"},
                { "id" : 12, "name" : "Name number 12", "grade" : 60, "passing" : 0, "sex" : "Male", "birth" : "2020-04-09", "place" : "Wiradesa"}

            ];

            arTable.init(".set-table-json", {

                data: json_table,
                title: "Data Orang",
                thead: ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"]

            });`,
            `0 : ["Sudrun", "XI", 90],
1 : ["Caridi", "X", 69]`
        ]
    }
    
    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "simple-init" ? "block" : "none"}}>
            <h1>Simple Init</h1>
            <div className="p-x">
                <div className="mb-1">
                    <strong>url</strong> and <strong>thead</strong> are required<br/>
                    <span>When you use <strong>init</strong> function, use should set "url" and "thead" for second parameters</span><br/>
                    <span>Which second parameters is object</span><br/>

                    <div className="mb-1 fs-16">Info : </div>
                    <span>When you use <strong>replace</strong> function, "url" and "thead" are not required</span><br/>

                    <div className="mb-1">No matters what the result is, when you have an array data or object data</div>
                    <strong>Example for array data : </strong><br/>
                    <div className="col-mx">
                        <pre className="syntax">{this.syntax[3]}</pre>
                    </div>

                    <strong>Example for object data : </strong>
                    <div className="col-mx">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>

                    <span>It'll automatically catch the data of your</span>
                    <br/><br/>
                    <h3 className="red dis-ib">WARNING:</h3> <span>Please to always be consistent in using JSON, either <strong>Array</strong> or <strong>Array of Object</strong></span>
                </div>
                <div className="bg-b px">
                    <pre className="syntax">{this.syntax[1]}</pre>
                </div>
                <h3>Output : </h3>
                <div class="set-table"></div>
                <div className="bg-b px">
                    <pre className="syntax">{this.syntax[2]}</pre>
                </div>
                <h3>Output : </h3>
                <div className="set-table-json"></div>
            </div>
        </div>
    }
}

export default ArjunaneTableV2DocsSimpleInit;