import React from 'react';

class ArjunaneDatePickerV2Init extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`$(document).tanggal('.date', {
    formatDate : "yyyy-mm-dd",
    rangeYears : "1950"
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "init" ? "block" : "none"}}>
                    <h1>Init</h1>
                    <div className="col-mx bg-w2 p-x">
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            <h3>Output:</h3>
                            <div className="f-input">
                                <span>Date</span>
                                <input className="date" type="text" placeholder="Date ..."/>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2Init;