import React from 'react';

class ArjunaneTableV2DocsPopupAction extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
            `arTable.init("#table-popup", {
                url     : "json.json",
                title   : "Data Orang",
                thead   : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"],
                popup_action  : 
                [
                    {
                        text    : "Edit",
                        cls     : "btn t1 blue",
                        href    : "edit.php",
                        params  : function (data)
                        {
                            var url     = this.href + "?id=" + data[0];
                            return url;
                        },
                        listener: function  (data, event, element)
                        {
                            event.preventDefault();
                            var url = element.getAttribute("href");
                            arTable.req(url, function (result) {
                                if(result.error === null && arTable.isJSON(result.result))
                                {
                                    // Success
                                }
                                else
                                {
                                    // Error
                                }
                            });
                        }
                    },
                    {
                        text    : "Delete",
                        cls     : "btn t1 red",
                        href    : "delete.php",
                        params  : function (data)
                        {
                            var url     = this.href + "?id=" + data[0];
                            return url;
                        }
                    }
                ]
            });`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "popup-action" ? "block" : "none"}}>
                    <h1>Popup Action</h1>
                    <div className="p-x">
                        <div className="mb-1">
                            <span>The <strong>action</strong> and <strong>popup_action</strong> are objects that defines a clickable button or specifies the URL of the page the link goes to.</span>
                        </div>
                        <strong>popup_action</strong> have 6 keys :
                        <ul>
                            <li>text : name or text of button</li>
                            <li>href : your link</li>
                            <li>cls [<i>Optional</i> ] : class of CSS (you can put your CSS on it)</li>
                            <li>attr [<i>Optional</i> ] : Your optional attributes</li>
                            <li>params [<i>Optional</i> ] : custom parameter for your link (href)
                                <ul>
                                    <li>data : get current data</li>
                                    <li>return string</li>
                                </ul>
                            </li>
                            <li>listener [<i>Optional</i> ] : your action for the listener
                                <ul>
                                    <li>data : get current data
                                        <ul>
                                            <li>key : get current index of data</li>
                                            <li>data : get current data</li>
                                        </ul>
                                    </li>
                                    <li>events</li>
                                    <li>object : object <strong>this</strong></li>
                                </ul>
                            </li>
                        </ul>
                        
                        <div className="mb-1">
                            <strong>Example</strong> :
                        </div>
                        <div className="bg-b px mb-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <div id="table-popup"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsPopupAction;