import React from 'react';

class ArjunaneDatePickerV2ShowDate extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default : [ true, false ]
// First Boolean : Date Picker
// Second Boolean : Time Chooser

showDate : 
[
    true, // => Set it to false if you don't want to see the Date
    true // => Set it to true if you want to see the Time
]

// Example :  if you want to show the Time Chooser only, then
showDate :
[
    false,
    true
]
`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "show-date" ? "block" : "none"}}>
                   <h3 id="showdate">Show Date</h3>
                    <div className="col-mx">
                        <span>type : <i>ARRAY</i> =&gt; <i>BOOLEAN, BOOLEAN</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="f-input">
                            <span>Date</span>
                            <input className="-showDate" type="text" placeholder="Date ..."/>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2ShowDate;