import React from 'react';

class ArjunaneFlutterRedirect extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`Redirect.route(context, 'about');`,

`route(
    BuildContext context, 
    String routeName,
    {
        RouterAnimationType type, 
        Duration duration, 
        Map<String, dynamic> withData 
    }
)`,

`Redirect.forward(context, 'about');`,

`forward(
    BuildContext context, 
    String routeName,
    {
        RouterAnimationType type, 
        Duration duration, 
        Map<String, dynamic> withData 
    }
)`,

`Redirect.back(BuildContext context);`,

`back(
    BuildContext context, 
    {
        RedirectData withData
    }
)`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "redirect" ? "block" : "none"}}>
                    <h1>Redirect class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <h3>route</h3>
                            <div className="col-mx">
                                The <strong>route</strong> method is push the given route onto the navigator, and then remove all the previous routes until the predicate returns true.
                            </div>
                            <pre className="syntax">{this.syntax[0]}</pre>

                            <h3>constructor</h3>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>routeName</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Route names, by convention, use a path-like structure (for example, '/a/b/c').
                                    </div>
                                    <div className="col-mx">
                                        <strong className="fc-r">NOTE</strong> : make sure that you have include all your pages in <strong>routes</strong> property (<strong>config.dart</strong>).
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>type</strong> &rarr; <span className="fc-b">RouterAnimationType</span>
                                    </div>
                                    <div className="col-mx">
                                        Transition animation between pages.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>duration</strong> &rarr; <span className="fc-b">Duration</span>
                                    </div>
                                    <div className="col-mx">
                                        Duration transition animation between pages.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Duration(milliseconds: 200)</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>withData</strong> &rarr; <span className="fc-b">Map&lt;String,dynamic&gt;</span>
                                    </div>
                                    <div className="col-mx">
                                        Put your data into next page.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Duration(milliseconds: 200)</i>
                                    </div>
                                </li>
                            </ul>

                            <h3>forward</h3>
                            <div className="col-mx">
                                The <strong>forward</strong> method is push the given route onto the navigator.
                            </div>
                            <pre className="syntax">{this.syntax[2]}</pre>

                            <h3>constructor</h3>
                            <pre className="syntax">{this.syntax[3]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>routeName</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Route names, by convention, use a path-like structure (for example, '/a/b/c').
                                    </div>
                                    <div className="col-mx">
                                        <strong className="fc-r">NOTE</strong> : make sure that you have include all your pages in <strong>routes</strong> property (<strong>config.dart</strong>).
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>type</strong> &rarr; <span className="fc-b">RouterAnimationType</span>
                                    </div>
                                    <div className="col-mx">
                                        Transition animation between pages.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>duration</strong> &rarr; <span className="fc-b">Duration</span>
                                    </div>
                                    <div className="col-mx">
                                        Duration transition animation between pages.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Duration(milliseconds: 200)</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>withData</strong> &rarr; <span className="fc-b">Map&lt;String,dynamic&gt;</span>
                                    </div>
                                    <div className="col-mx">
                                        Put your data into next page.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Duration(milliseconds: 200)</i>
                                    </div>
                                </li>
                            </ul>

                            <h3>back</h3>
                            <div className="col-mx">
                                The <strong>back</strong> method is pop the top-most route off the navigator..
                            </div>
                            <pre className="syntax">{this.syntax[4]}</pre>

                            <h3>constructor</h3>
                            <pre className="syntax">{this.syntax[5]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>withData</strong> &rarr; <span className="fc-b">RedirectData</span>
                                    </div>
                                    <div className="col-mx">
                                        Redirect back with data.
                                    </div>
                                </li>
                            </ul>

                            <h3>RedirectData</h3>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>previousController</strong> &rarr; <span className="fc-b">Controller</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Your previous Controller class/page.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>data</strong> &rarr; <span className="fc-b">dynamic</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Your data.
                                    </div>
                                </li>
                            </ul>

                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterRedirect;