import React from 'react';

class ArjunaneUnityColorFromHex extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`Color white_color = ar.ColorFromHex("#FFFFFF");`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "color-from-hex" ? "block" : "none"}}>
                   <h1>Color From Hex</h1>

                    <div className="col-mx">
                        <div className="col-mx">
                            <span>Method <strong>ColorFromHex</strong> is used to set color from Hex (string)</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityColorFromHex;