import React from 'react';

class ArjunaneTableV2DocsUpdateRow extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`update_row :
{
    3 : function (data)
    {
        var html = (data[3] !== 1) ? '&lt;span style="color:red;">Tidak Lulus&lt;/span>' : '&lt;span style="color:green">Lulus&lt;/span>';
        return html;
    }
}`,
`update_row :
{
    "passing" : function (data)
    {
        var html = (data["grade"] > 70) ? '&lt;span style="color:red;">Tidak Lulus&lt;/span>' : '&lt;span style="color:green">Lulus&lt;/span>';
        return html;
    }
}`
        ];
    }

    render() {
        return <div class="container nav-left" style={{display: this.props.uri === "update-row" ? "block" : "none"}}>
                    <h1>Update Row</h1>
                    <div class="p-x">
                        <div class="mb-1">
                            <span>This method is used to work with the data in the row.</span><br/>
                            <strong>NOTE</strong> : that when used as a setter, this method sets the data to apply to the table, but does not update the table's internal caches of data
                        </div>
                        <div class="bg-b px">
                            <pre class="syntax">{this.syntax[0]}</pre>
                        </div>
                        <div class="mt-1 mb-1">
                            <strong>OR</strong>
                        </div>
                        <div class="bg-b px">
                            <pre class="syntax">{this.syntax[1]}</pre>
                        </div>
                        <div class="col-mx">
                            <h3>Output:</h3>
                        </div>
                        <div id="update_row"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsUpdateRow;