import React from 'react';

class ArjunaneTableV2DocsFilter extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var filterArTable = arTable.init("#table-filter", {
    ...
});

// for array
document.getElementById("input-name").addEventListener("input", function () {
    var value        = this.value;
    var findForIndex = 1;
    filterArTable.filter(value, findForIndex);
});

document.getElementById("input-grade").addEventListener("input", function () {
    var value        = this.value;
    var findForIndex = 2;
    filterArTable.filter(value, findForIndex, "gte");
});

// for array of object
document.getElementById("input-name").addEventListener("input", function () {
    var value        = this.value;
    var findForKey = "name";
    filterArTable.filter(value, findForKey);
});

document.getElementById("input-grade").addEventListener("input", function () {
    var value        = this.value;
    var findForKey = "grade";
    filterArTable.filter(value, findForKey, "gte");
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "filter" ? "block" : "none"}}>
                   <h1>Filter</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>Sometimes you want to filter the data from the outside without use <strong>form_filter</strong> from <strong>content_top</strong> or <strong>content_bottom</strong>, then the <strong>filter</strong> method will useful for you.</span>
                        </div>
                        <div className="col-mx">
                            <span>The <strong>filter</strong> method takes 3 parameters:</span>
                            <ul>
                                <li>value : value from your <strong>input</strong> or <strong>select option</strong> and anymore</li>
                                <li>
                                    index : set your index to filter for specific data from your <strong>index</strong>
                                    <ul>
                                        <li>integer <strong>OR</strong> string</li>
                                        <li>Use "<strong>*</strong>" to find from all index<br/>
                                            <strong>WARNING:</strong> when you use '*' (star) to find all your data, it will replacing all your searches before
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    Searches type
                                    <ul>
                                        <li>io : IndexOf() [<strong>default</strong>]</li>
                                        <li>eq : Equal ( === )</li>
                                        <li>gt : Greater than ( &gt; )</li>
                                        <li>lt : Lower than ( &lt; )</li>
                                        <li>gte : Greater than Equal ( &gt;= )</li>
                                        <li>lte : Lower than Equal ( &lt;= )</li>
                                    </ul>
                                </li>
                            </ul>
                        </div>

                        <h3>Example :</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <div className="col-mx cf">
                            <div className="col-md-6 left">
                                <div className="f-input">
                                    <span>Name</span>
                                    <input type="text" id="input-name" placeholder="Find Name ..."/>
                                </div>
                            </div>
                            <div className="col-md-6 left">
                                <div className="f-input">
                                    <span>Grade (Greater than Equal)</span>
                                    <input type="text" id="input-grade" placeholder="Find Grade ..."/>
                                </div>
                            </div>
                        </div>
                        <div id="table--filter"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsFilter;