import React from 'react';

class ArjunaneUnityGetGameObject extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var panel_main = ar.Get("Panel_main");

panel_main.GameObject; // to get Game Object

var buttons = ar.Get("Buttons");

buttons.GameObjects; // to get all Game Objects
// NOTE: if you use buttons.GameObject instead of GameObjects, it will catch first GameObject of (from example) Buttons`,

`public class Example : MonoBehaviour
{
    public GameObject   _gameObject;
    public GameObject[] _gameObjects;

    void Start()
    {
        ar.Get(_gameObject);
        ar.Get(_gameObjects);
    }
}
                        `
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "get-gameobject" ? "block" : "none"}}>
                    <h1>Get Game Object</h1>
                    <div className="col-mx">
                        <span>Let's say you have some Components:</span>
                        <ul>
                            <li><strong>MainCamera</strong></li>
                            <li><strong>Directional Light</strong></li>
                            <li><strong>Canvas</strong></li>
                            <ul>
                                <li>Button_go</li>
                                <li>Button_exit</li>
                                <li>Buttons</li>
                                <li>Buttons</li>
                                <li>RawImage</li>
                                <li><strong>Panel_main</strong></li>
                                <li><strong>Panel</strong></li>
                                <ul>
                                    <li>Text_username</li>
                                    <li>InputField_username</li>
                                </ul>
                            </ul>
                            <li><strong>3D_object</strong></li>
                        </ul>
                    </div>
                    <div className="col-mx">
                        <h3>Get</h3>
                        <div className="col-mx">
                            <span>Get Your game object</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                        <div className="col-mx">
                            <span>Set your GameObject(s)</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[1]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityGetGameObject;