import React from 'react';

class ArjunaneFlutterFileServer extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`await FileServer.downloadFile( "https://your-link-to-download", "file-name.3gp", "your/path", 
    onProgress: (p) {
      
        // p.size;
        // p.progress;
        // p.percent;

    },
    onFinish: () {
      print("===== Download Selesai =======");
    },
    onError: (stackTrace) {
      print("===== Download Error =======");
    }
);`,

`downloadFile(
    String url, String fileName, String dir, {
        Function(int) beforeSend, Function(DownloadInfo) onProgress,
        Function() onFinish, Function(StackTrace) onError
    }
)`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "file-server" ? "block" : "none"}}>
                    <h1>File Server class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>DownloadFile</strong> lets you download data to your mobile app.
                        </div>
                        <div className="col-mx">
                            <h3>downloadFile</h3>
                            <pre className="syntax">{this.syntax[0]}</pre>

                            <h3>constructor</h3>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>url</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Link address
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>fileName</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        File Name
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>dir</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The directory path to save the file to your mobile app.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>beforeDownload</strong> &rarr; <span className="fc-b">Function(int)</span>
                                    </div>
                                    <div className="col-mx">
                                        A function to run before the request <strong>onProgress</strong> is sent.
                                    </div>
                                    <div className="col-mx">
                                        This method have integer parameters callback which it is the size of the response body, in bytes from <span className="fc-b">StreamseResponse contentLength</span>.
                                    </div>
                                    <div className="col-mx">
                                        Sometimes you want to calculate the size of the file, so <strong>beforeDownload</strong> one of the best choice for you.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onProgress</strong> &rarr; <span className="fc-b">Function(DownloadFile)</span>
                                    </div>
                                    <div className="col-mx">
                                        The progress of downloading a file.
                                    </div>
                                    <div className="col-mx">
                                        The <strong>DownloadFile</strong> properties:
                                        <ul>
                                            <li>
                                                <div className="col-mx">
                                                    <strong>size</strong> &rarr; <span className="fc-b">double</span>
                                                </div>
                                                <div className="col-mx">
                                                    To get the size of a file in bytes
                                                </div>
                                            </li>
                                            <li>
                                                <div className="col-mx">
                                                    <strong>progress</strong> &rarr; <span className="fc-b">double</span>
                                                </div>
                                                <div className="col-mx">
                                                    To get the progress of a file in bytes
                                                </div>
                                            </li>
                                            <li>
                                                <div className="col-mx">
                                                    <strong>percent</strong> &rarr; <span className="fc-b">double</span>
                                                </div>
                                                <div className="col-mx">
                                                    To get the percentage of downloading a file (0-100%)
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onFinish</strong> &rarr; <span className="fc-b">Function()</span>
                                    </div>
                                    <div className="col-mx">
                                        When the downloading a file is done.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onError</strong> &rarr; <span className="fc-b">Function(StackTrace)</span>
                                    </div>
                                    <div className="col-mx">
                                        When the downloading a file is error.
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterFileServer;