import React from 'react';

class ArjunaneDatePickerV2PermanentHolidays extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// ["month-day", "the name of your holiday"]
// Default permanentHolidays :
["00-01", "Tahun Baru"],
["06-01", "Hari Lahir Pancasila"],
["07-17", "Hari Kemerdekaan Indonesia"],
["05-01", "Hari Lahir Pancasila"],
["11-25", "Hari Raya Natal"]

// NOTE: First value of month is zero ("00" OR January) until 11 (December)

// Set to FALSE if you don't want to show the Permanent Holidays
permanentHolidays : false`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "permanent-holidays" ? "block" : "none"}}>
                    <h3 id="permanentholidays">Permanent Holidays</h3>
                    <div className="col-mx">
                        <span>type : <i>ARRAY =&gt; STRING, STRING</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2PermanentHolidays;