import React from 'react';

class ArjunaneFlutterCloseDialog extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`closeDialog(context);`,

`void closeDialog(BuildContext context)`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "close-dialog" ? "block" : "none"}}>
                    <h1>Close Dialog</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>closeDialog</strong> method to close Alert Dialog.</span>
                        </div>
                        <h3>closeDialog</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterCloseDialog;