import React from 'react';

class ArjunaneDatePickerV2MonthsName extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default:
monthsName: 
[
    "Januari", 
    "Februari", 
    "Maret", 
    "April", 
    "Mei", 
    "Juni", 
    "Juli", 
    "Agustus", 
    "September", 
    "Oktober", 
    "November", 
    "Desember"
]`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "months-name" ? "block" : "none"}}>
                    <h3 id="monthsname">Months Name</h3>
                    <div className="col-mx">
                        <span>type : <i>ARRAY</i> =&gt; STRING</span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2MonthsName;