import React from 'react';

class ArjunaneFlutterStandards extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`Header("This is a Header-check it out!")`,

`Header(
    String text, {
        Color fontColor, TypeHeader typeHeader = TypeHeader.h1, 
        TextAlign textAlign, FontStyle fontStyle
    }
)`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "standards" ? "block" : "none"}}>
                    <h1>Standards</h1>
                    <div className="p-x">
                        
                        <h3>Header</h3>
                        <div className="col-mx">
                            <span>The <strong>alertContainer</strong> element represents a container for introductory content or a set of navigational links</span>
                        </div>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            
                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>text</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The text to display.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>fontColor</strong> &rarr; <span className="fc-b">Color</span>
                                    </div>
                                    <div className="col-mx">
                                        The font color of the surface of this <strong>heading</strong>.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>fontStyle</strong> &rarr; <span className="fc-b">FontStyle</span>
                                    </div>
                                    <div className="col-mx">
                                        Whether to slant the glyphs in the font.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>style</strong> &rarr; <span className="fc-b">TypeHeading</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">TypeHeader.h1</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>textAlign</strong> &rarr; <span className="fc-b">TextAlign</span>
                                    </div>
                                    <div className="col-mx">
                                        How the text should be aligned horizontally..
                                    </div>
                                </li>
                            </ul>
                            
                            <h3>TypeHeader enum</h3>
                            <h4>constant</h4>
                            <ul>
                                <li>h1</li>
                                <li>h2</li>
                                <li>h3</li>
                                <li>h4</li>
                                <li>h5</li>
                                <li>h6</li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterStandards;