import React from 'react';

class ArjunaneDatePickerV2FormatTime extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default : "hh:mm:ss"
// Another Format Time :
"hh:mm:ss:ms",
"hh:mm:ss",
"hh:mm",
"hh"

// Example :
formatTime : "hh:mm"`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "format-time" ? "block" : "none"}}>
                    <h3 id="formattime">Format Time</h3>
                    <div className="col-mx">
                        <span>type : <i>STRING</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            <h3>Output:</h3>
                            <div className="f-input">
                                <span>Date</span>
                                <input className="-date" type="text" placeholder="Date ..."/>
                            </div>
                        </div>
                    </div>

                </div>;
    }
}

export default ArjunaneDatePickerV2FormatTime;