import React from 'react';

class ArjunaneFlutterConfig extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`static final Map<String, Widget> routes = {
    '/' : LoginPage(),
    'home' : HomePage()
};

static final String baseRoute = '/';

static final List<SingleChildWidget> providers = [
    ChangeNotifierProvider(create: (BuildContext context) => HomeModel())
];`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "config" ? "block" : "none"}}>
                    <h1>Config class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The Config class provides a means to retrieve configuration preferences. These preferences can come from the default config file (app/config/configs.dart).</span>
                        </div>
                        
                        <h3>Property</h3>
                        <div className="col-mx">
                            <ul>
                                <li>
                                    <strong>Routes</strong>
                                    <div className="col-mx">
                                        The <strong>routes</strong> property have a collection Key and Value with:
                                    </div>
                                    <ul>
                                        <li>key/name : string</li>
                                        <li>Page class : StatefulWidget</li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>Base Route</strong>
                                    <div className="col-mx">
                                        The <strong>baseRoute</strong> property that have value as a string
                                    </div>
                                </li>
                                <li>
                                    <strong>Providers</strong>
                                    <div className="col-mx">
                                        The <strong>providers</strong> property is an array that have an <i>EfficientLengthIterable</i> Model (or provider) class
                                    </div>
                                </li>
                            </ul>
                            
                        </div>
                        
                        <pre className="syntax">{this.syntax[0]}</pre>

                    </div>
                </div>;
    }
}

export default ArjunaneFlutterConfig;