import React from 'react';

class ArjunaneUnityWeb extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`StartCoroutine( ar.GetURL("your-url").sendWebRequest( (result, is_success, error) => {
    if(is_success)
    {
        // <i>your action or convert the 'result' to JSON</i>
    }
    else
    {
        // <i>Error : your handled</i>
    }
}) );`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "web" ? "block" : "none"}}>
                  <h1>Web</h1>
                    <div className="col-mx">
                        <strong>NOTE:</strong> <span>Please use HTTPS, because sometimes it didn't work if you use HTTP for request or Unity will show Error if you use HTTP for online request</span>
                    </div>
                    <div className="col-mx">
                        <strong>Get string from your Website</strong>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityWeb;