import React from 'react';
//import {Route, Switch} from 'react-router-dom';
import helper from '../../../helper/helper';
import ArjunaneLeft from '../container_left';
import ArjunaneDatePickerV2Holidays from './docs/adp_10_holidays';
import ArjunaneDatePickerV2PermanentHolidays from './docs/adp_11_permanent_holidays';
import ArjunaneDatePickerV2MultipleDate from './docs/adp_12_multiple_date';
import ArjunaneDatePickerV2Attribute from './docs/adp_13_attribute';
import ArjunaneDatePickerV2Home from './docs/adp_1_home';
import ArjunaneDatePickerV2Init from './docs/adp_2_init';
import ArjunaneDatePickerV2DaysName from './docs/adp_4_days_name';
import ArjunaneDatePickerV2FormatDate from './docs/adp_3_format_date';
import ArjunaneDatePickerV2MonthsName from './docs/adp_5_months_name';
import ArjunaneDatePickerV2RangeYears from './docs/adp_6_range_years';
import ArjunaneDatePickerV2ShowDate from './docs/adp_7_show_date';
import ArjunaneDatePickerV2FormatTime from './docs/adp_8_format_time';
import ArjunaneDatePickerV2MinutesHours from './docs/adp_9_minutes_hours';

class ArjunaneDatePickerV2Container extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'javascript');
        });
        

        helper.appendCSS(process.env.PUBLIC_URL + "/plugins/date-picker-v2/Scripts/arjunane-date.min.css");
        helper.appendScript(process.env.PUBLIC_URL + "/plugins/date-picker-v2/Scripts/jquery.js", () => {
            helper.appendScript(process.env.PUBLIC_URL + "/plugins/date-picker-v2/Scripts/arjunane-date.min.js", () => { 
                window.$(document).tanggal('.date', {
                    formatDate: "yyyy-mm-dd",
                    rangeYears: "1950",
                    showDate: [true, false],
                    multiSetDate : false
                });
                window.$(document).tanggal('.-showDate', {
                    formatDate: "yyyy-mm-dd",
                    rangeYears: "1950",
                    showDate: [false, true],
                    multiSetDate : false
                });
                window.$(document).tanggal('.-date', {
                    formatDate: "yyyy-mm-dd",
                    rangeYears: "2010",
                    showDate: [true, true],
                    formatTime: "hh:mm",
                    multiSetDate : false
                });
                window.$(document).tanggal('#date-picker', {
                    formatDate: "yyyy-mm-dd",
                    rangeYears: "1950",
                    showDate: [true, false],
                    multiSetDate : false
                });
                window.$(document).tanggal('.multi', {
                    formatDate: "yyyy-mm-dd",
                    rangeYears: "1950",
                    showDate: [true, false],
                    multiSetDate : true
                });
            }); 
        });
        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-date-picker-v2" navClick={this.navigationsClick}/>
                    
                    <ArjunaneDatePickerV2Home uri={this.state.uri}/>
                    <ArjunaneDatePickerV2Init uri={this.state.uri}/>
                    <ArjunaneDatePickerV2FormatDate uri={this.state.uri}/>
                    <ArjunaneDatePickerV2DaysName uri={this.state.uri}/>
                    <ArjunaneDatePickerV2MonthsName uri={this.state.uri}/>
                    <ArjunaneDatePickerV2RangeYears uri={this.state.uri}/>
                    <ArjunaneDatePickerV2ShowDate uri={this.state.uri}/>
                    <ArjunaneDatePickerV2FormatTime uri={this.state.uri}/>
                    <ArjunaneDatePickerV2MinutesHours uri={this.state.uri}/>
                    <ArjunaneDatePickerV2Holidays uri={this.state.uri}/>
                    <ArjunaneDatePickerV2PermanentHolidays uri={this.state.uri}/>
                    <ArjunaneDatePickerV2MultipleDate uri={this.state.uri}/>
                    <ArjunaneDatePickerV2Attribute uri={this.state.uri}/>

                </React.Fragment>;
    }
}

export default ArjunaneDatePickerV2Container;