import React from 'react';

class ArjunaneTableV2DocsUpdateColumn extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var updateColumnArTable = arTable.init("#table-update-column", {
    ...
});

document.getElementById("btn-update-column").addEventListener("click", function () { 
    updateColumnArTable.updateColumn(2, {
        value : function (currentRow)
        {
            var grade = currentRow[2] + 15;
            if(grade > 100) return currentRow[2];

            return grade;
        }
    });
    updateColumnArTable.updateColumn(3, {
        value : function (currentRow)
        {
            return currentRow[2] >= 70 ? 1 : 0;
        },
        html : function (currentRow)
        {
            return currentRow[3] === 1 ? "Lulus" : "Tidak Lulus";
        }
    });
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "update-column" ? "block" : "none"}}>
                   <h1>Update Column</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            Method of <strong>updateColumn</strong> is for updating data into table's internal caches of data.
                        </div>
                        <div className="col-mx">
                            <strong>updateColumn</strong> take 2 parameters
                            <ul>
                                <li>key or index</li>
                                <li>Objects
                                    <ul>
                                        <li>value : method/function with return</li>
                                        <li>html : method/function with return of string</li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                        <h3>NOTE</h3>
                        <ul>
                            <li>The property <strong>value</strong> is for inserting to local caches of data</li>
                            <li>The property <strong>html</strong> is output from <strong>value</strong></li>
                            <li>When you use <strong>updateColumn</strong> method, it will remove all your recent searches data from input or others</li>
                        </ul>

                        <h3>Example for deleteColumn method:</h3>
                        <div className="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <button id="btn-update-column">Update Columns 2 and 3</button>
                        <div id="table-update-column"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsUpdateColumn;