import React from 'react';

class ArjunaneTableV2DocsShowAlert extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var alertArTable = arTable.init("#table-alert", {
    ...
});

document.getElementById("alert-button").addEventlistener("click", function () {
    alertArTable.showAlert({
        type        : "success",
        title       : "BERHASIL",
        text        : "Successfully deleted the data",
        button_text : "Ok"
    });
});`,

`var alertArTable = arTable.init("#table-alert", {
    url     : "json/json.json",
    title   : "Data Orang",
    checkable: true,
    thead   : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"],
    action  : 
    [
        {
            text    : "Delete",
            cls     : "btn t1 red",
            href    : "json/delete.json",
            listener: function(data, event, element) 
            {
                event.preventDefault();
                var url = element.getAttribute("href");
                arTable.req(url, function(res) {
                    if (res.error === null && arTable.isJSON(res.result)) 
                    {
                        var json = JSON.parse(res.result);
                        if (json.hasil) {
                            var name = data[1];
                            alertArTable.deleteData(data.key);
                            alertArTable.showAlert({
                                type: "success",
                                title: "BERHASIL",
                                text: "Successfully deleted the data : " + name,
                                button_text: "Ok"
                            });
                        }
                    } else {
                        // Error
                    }
                });
            }
        }
    ],
    content_top: 
    {
        action: 
        [
            {
                text    : "Delete Selected",
                type    : "success",
                href    : "json/delete.json",
                listener: function(data, event, element) {
                    event.preventDefault();
                    if (data.length > 0) 
                    {
                        arTable.req(this.href, function(res) {
                            if (res.error === null && arTable.isJSON(res.result)) 
                            {
                                var json = JSON.parse(res.result);
                                if (json.hasil) {
                                    for(var i = 0; i < data.length; i++)
                                    {
                                        alertArTable.deleteData(data[i].key)
                                    }
                                    alertArTable.showAlert({
                                        type: "success",
                                        title: "BERHASIL",
                                        text: "Successfully deleted the data",
                                        button_text: "Ok"
                                    });
                                }
                            } else {
                                // Error
                            }
                        });
                    } else {
                        alertArTable.showAlert({
                            type: "danger",
                            title: "Ops",
                            text: "Please at least choose one row",
                            button_text: "Ok"
                        });
                    }
                }
            }
        ]
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "show-alert" ? "block" : "none"}}>
                   <h1>Show Alert</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>showAlert</strong> method displays an alert box with a specified message and an OK button.
                        </div>
                        <div className="col-mx">
                            <strong>showAlert</strong> have 4 keys:
                            <ul>
                                <li>
                                    type
                                    <ul>
                                        <li>warning</li>
                                        <li>info (<strong>Default</strong>)</li>
                                        <li>success</li>
                                        <li>danger</li>
                                    </ul>
                                </li>
                                <li>title : string (Default is : "INFO")</li>
                                <li>text : string (Default is : "Info Alert")</li>
                                <li>button_text : string (Default is : "OK")</li>
                            </ul>
                        </div>
                        <h3>Example</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        
                        <h3>Example Show Alert</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>

                        <h3>Output:</h3>
                        <div id="table-alert"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsShowAlert;