import React from 'react';

class ArjunaneXamarinGridCount extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`int column = 3;// How many columns did you want to set
var gridCount = ar.SetGridCount(column);
var data = JsonConvert.DeserializeObject<YourModel[]>(ser);

// Let's say you have grid with name "grid"
for(int i = 0; i < data.Length; i++)
{
    var getCount = ar.GetGridCount(i);
    var ini = data[i];
    insertFrame(ini, getCount.row, getCount.column);
}


    /*
        Let's say variable data have 10 data and the output will :
        {insertFrame Grid 0} {insertFrame Grid 1} {insertFrame Grid 2}
        {insertFrame Grid 3} {insertFrame Grid 4} {insertFrame Grid 5}
        {insertFrame Grid 6} {insertFrame Grid 7} {insertFrame Grid 8}
        {insertFrame Grid 9}
    */


// Let's say you have a function and it will append content to the Grid
void insertFrame(M_help data, int row, int column)
{
    var frame = new Frame() { 
        Padding = 10,
    };

    frame.Content = new Label()
    {
        Text = data.judul
    };

    var tap = new TapGestureRecognizer();
    tap.Tapped += (s, e) => 
    {
        Navigation.PushAsync(new HelpDetail(data) { Title = data.judul });
    };

    frame.GestureRecognizers.Add(tap);

    grid.Children.Add(frame, row, column);
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "grid-count" ? "block" : "none"}}>
                    <h3>Grid Count</h3>
                    <div className="col-mx p-l-3">
                        <strong>Grid Count</strong> will generate Count/Index for Grid (Column and Row).<br/>
                        Methods that used for count is <strong>SetGridCount()</strong> and <strong>GetGridCount()</strong>.
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>SetGridCount
                                        <ul>
                                            <li>Integer</li>
                                        </ul>
                                    </li>
                                    <li>GetGridCount
                                        <ul>
                                            <li>Integer</li>
                                        </ul>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinGridCount;