import React from 'react';

class ArjunaneDatePickerV2RangeYears extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default : Last Year and Now

// Particular Years and ':' for Separator
rangeYears : "1980:2005"

// OR choose your last year
rangeYears :"1980" // It mean that 1980 until Now`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "range-years" ? "block" : "none"}}>
                    <h3 id="monthsname">Range Years</h3>
                    <div className="col-mx">
                        <span>type : <i>STRING</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2RangeYears;