import React from 'react';

class ArjunaneFlutterRequest extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`if(request.has("name"))
{

}`,

`var data = request.get("name");`,

`class VHomeView extends View<CHomeController> {

    VHomeView (CHomeController controller) : super(controller);
  
    @override
    Widget build(BuildContext context) {
  
      return Scaffold(
        appBar: AppBar(
          title: Text("Your Title")
        ),
        body: Container(
            child: Buttons("Next", onPressed: () {
                Redirect.forward('detail', withData : {
                    'name'      : "tamVan dan berani",
                    "initial"   : "tamVan",
                    "level"     : ["tamvan", "dan", "berani"]
                });
            })
        ),
     );
  
    }
  
}`,

`class CDetailController extends Controller<CDetailPage> {

    @override
    void initState() {
        print(request.get("name")); // ouput : tamVan dan berani
        print(request.get("initial")); // output : tamVan
        print(request.get("level")); // output : List => ["tamvan", "dan", "berani"]
        // TODO: implement initState
        super.initState();
    }
}`,

`class VDetailView extends View<CDetailController> {

    VDetailView (CDetailController controller) : super(controller);
  
    @override
    Widget build(BuildContext context) {
  
      return Scaffold(
        appBar: AppBar(
          title: Text("Your Title")
        ),
        body: Container(
            child: Buttons("Next", onPressed: () {
                Redirect.back('/', 
                    withData : new RedirectData(
                        CHomeController(), 
                        "mencoba saja"
                    )
                );
            })
        ),
     );
  
    }
  
}`,

`class CHomeController extends Controller<CHomePage> {

    @override
    void onBackData(data) {
        print("isi dari onBackData : $data"); // output : mencoba saja
        super.onBackData(data);
    }
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "request" ? "block" : "none"}}>
                    <h1>Request class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>Request</strong> is to get data from Redirect.
                        </div>
                        <div className="col-mx">
                            <strong className="fc-r">NOTE</strong> : The <strong>request</strong> only available in <strong>Controller</strong>.
                        </div>
                        <div className="col-mx">

                            <h3>has</h3>
                            <div className="col-mx">
                                You may use the <span className="fc-b">has</span> method to determine if a value is present on the request. The <span className="fc-b">has</span> method returns true if the value is present on the request:
                            </div>
                            <pre className="syntax">{this.syntax[0]}</pre>

                            <h3>get</h3>
                            <div className="col-mx">
                                Get value request from Redirect.
                            </div>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h2>Example 1</h2>
                            <div className="col-mx">
                                VHomeView.dart
                            </div>
                            <pre className="syntax">{this.syntax[2]}</pre>
                            
                            <div className="col-mx">
                                CDetailController.dart
                            </div>
                            <pre className="syntax">{this.syntax[3]}</pre>

                            <h2>Example 2</h2>
                            <div className="col-mx">
                                VDetailView.dart
                            </div>
                            <pre className="syntax">{this.syntax[4]}</pre>
                            
                            <div className="col-mx">
                                CHomeController.dart
                            </div>
                            <pre className="syntax">{this.syntax[5]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterRequest;