import React from 'react';

class ArjunaneXamarinWebAddFile extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`webs.AddFile("photo", stream_photo);
webs.AddFile("video", stream_video);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "add-file" ? "block" : "none"}}>
                    <h3>Add File</h3>
                    <div className="col-mx p-l-3">
                        <strong>AddFile()</strong> is for adding file to Request.<br/>
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>Stream</li>
                                </ul>
                            </li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinWebAddFile;