import React from 'react';
import InitFirebase from '../../model/ModelFirebase';
import ProjectDetail from './project_detail';

class MainRightProjects extends React.Component {

    constructor(props) {
        super(props);

        this.categorys = [
            {text: "All", category: ""},
            {text: "AR", category: "augmented reality"},
            {text: "Blender 3D", category: "blender"},
            {text: "CSS/Javascript", category: "javascript,css"},
            {text: "Mobile", category: "android"},
            {text: "Website", category: "node js,php"},
            {text: "Others", category: "other"},
        ];
        this.isClicked  = false;
        this.isShow     = true;
        this.onClickedProject = this.onClickedProject.bind(this);
    }

    clickCategory()
    {
        if(this.isClicked) return;

        var categoryButtons = document.getElementsByClassName("category-button");
        var categoryShow    = document.getElementsByClassName("category-show")[0];
        var categoryList    = document.getElementsByClassName("category-list")[0];
        
        if(this.isShow)
        {
            categoryShow.classList.add("active");
            categoryList.classList.add("active");
        }
        else
        {
            categoryShow.classList.remove("active");
            categoryList.classList.remove("active");
        }

        for(var i = 0; i < categoryButtons.length; i++)
        {
            var ini = categoryButtons[i];
            this.active(ini, i, ( (categoryButtons.length - 1) - i ) === 0, !this.isShow);
        }
    }

    active(element, index, isLast, isRemove)
    {
        setTimeout( () => {
            if(isLast) 
            {
                this.isShow     = isRemove;
                this.isClicked  = false;
            }
            if(isRemove)element.classList.remove("active");
            else element.classList.add("active");
        }, 100 * index);
    }
    onClickedProject(data)
    {
        this.props.isDetailClicked(data);
    }

    chooseCategory(category, text)
    {
        var categorySelected    = document.getElementById("category-selected");
        var project             = document.getElementsByClassName("project");
        var split               = category.split(",");

        categorySelected.innerHTML = text;
        
        for(var i = 0; i < project.length; i++)
        {
            var pro     = project[i],
                tagFor  = pro.getAttribute('tag');
            if(category === "")
            {
                pro.classList.remove("inactive");
            }
            else
            {
                var isFind = false;
                var attr = tagFor.toLowerCase();
                for(var s = 0; s < split.length; s++)
                {
                    if(tagFor === 'node js,php' && attr.indexOf("android") !== -1) break;
                    if(attr.indexOf(split[s]) !== -1) isFind = true;
                }
                if(isFind) pro.classList.remove("inactive");
                else pro.classList.add("inactive");
            }
        }
    }

    render() {
        
        var nameClass = this.props.nameClass;
        return(
            <div class={"card-content" + ( nameClass === "portfolio" ?" active" : "")} id="projects">
                <div class="m-b-3 m-t-2 categorys">
                    <div class="category-show" onClick={this.clickCategory.bind(this)}>
                        <span></span>
                        <span>CATEGORY : </span>
                        <span id="category-selected">ALL</span>
                    </div>
                    <div class="category-list">
                        <div class="category-list-wrap">
                        <div class="category-list-inner">
                            {
                                this.categorys.map( (val) => {
                                    return <div class="category-button" for={val.category} onClick={() => {
                                        this.chooseCategory(val.category, val.text);
                                        this.clickCategory();
                                    }}>
                                                <span>{val.text}</span>
                                            </div>;
                                })
                            }
                                {/* <div class="category-button" for="">
                                    <span>ALL</span>
                                </div>
                                <div class="category-button" for="augmented reality">
                                    <span>AR</span>
                                </div>
                                <div class="category-button" for="blender">
                                    <span>Blender 3D</span>
                                </div>
                                <div class="category-button" for="javascript,css">
                                    <span>CSS/Javascript</span>
                                </div>
                                <div class="category-button" for="android">
                                    <span>Mobile</span>
                                </div>
                                <div class="category-button" for="node js,php">
                                    <span>Website</span>
                                </div>
                                <div class="category-button" for="other">
                                    <span>Others</span>
                                </div> */}
                        </div>
                        </div>
                    </div>
                </div>
                <div id="container-projects">
                    <InitFirebase projectClicked={this.onClickedProject} isDone={this.props.isDone}/>
                </div>
            </div>
        );
    }
}

export default MainRightProjects;