import React from 'react';

class ArjunaneTableV1DocsHome extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
            `<script src="arjunane_table.js"></script> 
// OR 
<script src="arjunane_table.min.js"></script>`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "" || this.props.uri === null ? "block" : "none"}}>
                    <div className="col-mx center">
                        <h1>-=[ MY Datatables.js DEMO ]=-</h1>
                        <div className="col-mx">
                            <span>Dependency-free JavaScript library Arjunane Datatables</span>
                        </div>
                    </div>
                    <div className="col-mx">
                        <strong>Required:</strong>
                        <ul>
                            <li>jQuery.js</li>
                            <li>datatables.css</li>
                            <li>datatables.js</li>
                        </ul>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV1DocsHome;