import React from 'react';

class ArjunaneXamarinWebSendWebFormMultipart extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`webs.GetURL("https://your-url.com");
webs.AddField("name", entry_name.Text);
webs.AddField("email", entry_email.Text);
webs.AddFile("photo", Stream_photo);
webs.SendWebFormMultipart( (is_success, result, error) => {
    if(is_success)
    {
        // Your action or Serialized <strong>result</strong> JSON
    }
    else
    {
        // Handle the Error or DisplayAlert("Error", error, "OK");
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "send-web-form-multipart" ? "block" : "none"}}>
                    <h3>Send Web Form Multipart</h3>
                    <div className="col-mx p-l-3">
                        <strong>SendWebFormMultipart()</strong> is for sending the Request form with File.<br/>
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>Action
                                        <ul>
                                            <li>Boolean</li>
                                            <li>String</li>
                                            <li>String</li>
                                        </ul>
                                    </li>
                                    <li>Integer (Timeout) : Default is 30 seconds</li>
                                </ul>
                            </li>
                            <li>Method : POST</li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinWebSendWebFormMultipart;