import React from 'react';

class ArjunaneUnityClone extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var parent = ar.Get("Parent_panel").GameObject;
var child  = ar.Get("Child_panel").GameObject;

ar.Clone(parent, child, 1);

// With ForEach
GameObject parent   = ar.Get("Parent_panel").SetEl();

ar.Get("Childs").ForEach(delegate(GameObject ini, int i) {
    ar.Clone(parent, ini);
});

ar.Clone(
    GameObject parent,
    GameObject child,
    int how_many, // ( = default is 1)
    bool world_position // ( = default is false)
);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "clone" ? "block" : "none"}}>
                   <h1>Clone</h1>
                    <div className="col-mx">Clone GameObjects</div>
                    <div className="col-mx">
                        <div className="bg-w2 p-x">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityClone;