import React from 'react';

class ArjunaneTableV1DocsSimpleInit extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`url : "https://your-url",

// or you if you have a json file

json : "https://json-file.json"

$('#dt-1').table({
    //url : "Scripts/",
    json: "Scripts/json.json",
    thead: ["ID", "Name", "Grade", "Gender", "Birth", "Place"],
});`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "simple-init"? "block" : "none"}}>
                    <h1>Tutorials : 1. Simple Init</h1>
                    <div className="col-mx">
                        <span>Simple init</span>
                    </div>
                    <div className="col-mx bg-w2 p-x">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV1DocsSimpleInit;