import React from 'react';

class ArjunaneTableV2DocsObjectParameters extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
            `<script src="arjunane_table.js"></script> 
// OR 
<script src="arjunane_table.min.js"></script>`
        ];
    }

    render() {
        return <div class="container nav-left" style={{display: this.props.uri === "object-parameters" ? "block" : "none"}}>
                    <h1>Object Parameters</h1>
                    <div class="p-x">
                        <div class="col-mx">
                            <ul>
                                <li>
                                    <strong>url</strong> [string]
                                    <ul>
                                        <li>Your url json.</li>
                                        <li><strong>Example:</strong>  <br/>
                                            <pre class="syntax">url : "json/json.json"</pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>title</strong> [string]
                                    <ul>
                                        <li>Give title for your table or you can leave it empty.</li>
                                        <li><strong>Example:</strong> <br/>
                                            <pre class="syntax">title : "Data Orang"</pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>thead</strong> [array of string]
                                    <ul>
                                        <li>Your thead column name.</li>
                                        <li><strong>Example:</strong> <br/>
                                            
                                            <pre class="syntax">thead : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"]</pre>
                                        </li>
                                        <li>
                                            <strong>NOTE:</strong> if you use a <strong>table</strong> as container, it not required.</li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>checkable</strong> [boolean]
                                    <ul>
                                        <li>Set it to <strong>true</strong> then the checkbox will showed (default is <strong>false</strong>).</li>
                                        <li><strong>Example:</strong> <br/>
                                            <pre class="syntax">checkable : true</pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>numeric</strong> [boolean]
                                    <ul>
                                        <li>Set it to <strong>true</strong> then the numbers will showed (default is <strong>false</strong>).</li>
                                        <li><strong>Example:</strong> <br/>
                                            <pre class="syntax">numeric : true</pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>scrollable</strong> [boolean]
                                    <ul>
                                        <li>Set it to <strong>false</strong> and the <strong>Scroll</strong> will be hide (default is <strong>true</strong>).</li>
                                        <li><strong>Example:</strong> <br/>
                                            <pre class="syntax">scrollable : true</pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>height</strong> [integer]
                                    <ul>
                                        <li><strong>NOTE</strong> : Be carefully if you set <strong>scrollable</strong> to false and you set the height of table.</li>
                                        <li><strong>Example:</strong> <br/>
                                            <pre class="syntax">height : 800</pre>
                                        </li>
                                    </ul>
                                </li>
                                <li>
                                    <strong>hide</strong> [array]
                                    <ul>
                                        <li><strong>Example:</strong> <br/>
                                            <pre class="syntax">hide : [0, 1]</pre>
                                            <strong>OR</strong>
                                            <br/>
                                            <pre class="syntax">hide : ["id", "password"]</pre>
                                        </li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsObjectParameters;