import React from 'react';

class ArjunaneXamarinHome extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`using ArjunaneXamarin;
namespace tamVan
{
    [XamlCompilation(XamlCompilationOptions.Compile)]
    public partial class Pengaturan : ContentPage
    {
        Arjunane ar = new Arjunane();
    }
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "" || this.props.uri === null ? "block" : "none"}}>
                    <div className="col-mx center">
                        <h1>-=[ Arjunane.cs DEMO ]=-</h1>
                        <div className="col-mx">
                            <span>Dependency-free C# library for Xamarin Forms</span>
                        </div>
                    </div>

                    <div className="col-mx">
                        <strong>Required:</strong>
                        <ul>
                            <li>Arjunane.cs</li>
                        </ul>
                    </div>

                    <h1>Init</h1>
                    <div className="col-mx">
                        <div className="bg-w2 p-x">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinHome;