import React from 'react';

class ArjunaneUnitySetActive extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var panels = ar.Get("Panel");
panels.SetActive(false);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "set-active" ? "block" : "none"}}>
                    <h1>Set Active</h1>

                    <div class="col-mx">
                        <div class="col-mx">
                            <span>Set visible to your GameObject(s)</span>
                        </div>
                        <div class="col-mx">
                            <div class="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnitySetActive;