
import React from 'react';

class ArjunaneTableV1DocsArrayInfo extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`array_info: [
    { index: 3, value: { 
            "0" : '&lt;span style="color:red">Not Pass&lt;/span>',
            "1" : '&lt;span style="color:green">Pass&lt;/span>'
        }
    }
]`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "array-info" ? "block" : "none"}}>
                    <h1>Tutorials : 4. Array Info</h1>
                    <div className="col-mx">
                        <ul>
                            <li>array_info :</li>
                            <ul>
                                <li>index : INT</li>
                                <li>value : ARRAY =&gt; STRING, STRING</li>
                            </ul>
                        </ul>
                    </div>
                    <div className="col-mx bg-w2 p-x">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV1DocsArrayInfo;