import React from 'react';

class ArjunaneTableV2DocsOthers extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var show_data = document.getElementById("show-data");

document.getElementById("clear-checked").addEventListener("click", function () {
    othersArTable.clearChecked();
})
document.getElementById("get-data").addEventListener("click", function () {
    show_data.innerHTML = JSON.stringify(othersArTable.getData(), null, 4);
})
document.getElementById("get-checked").addEventListener("click", function () {
    show_data.innerHTML = JSON.stringify(othersArTable.getCheckedData(), null, 4);
})
document.getElementById("get-current").addEventListener("click", function () {
    show_data.innerHTML = JSON.stringify(othersArTable.getCurrentData(), null, 4);
})
document.getElementById("get-all-current").addEventListener("click", function () {
    show_data.innerHTML = JSON.stringify(othersArTable.getAllCurrentData(), null, 4);
})
document.getElementById("get-pages").addEventListener("click", function () {
    show_data.innerHTML = JSON.stringify(othersArTable.pages(), null, 4);
})
document.getElementById("next-page").addEventListener("click", function () {
    othersArTable.nextPage();
})
document.getElementById("prev-page").addEventListener("click", function () {
    othersArTable.prevPage();
})
document.getElementById("jump-page").addEventListener("click", function () {
    var jump = document.getElementById("jump-input").value;
    othersArTable.jumpTo(jump);
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "others" ? "block" : "none"}}>
                   <h1>Others</h1>
                    <div className="p-x">
                        <ul>
                            <li>getData()
                                <div className="col-mx">
                                    The <strong>getData()</strong> method is used to get all data from your first json (from first request)
                                </div>
                            </li>
                            <li>getCurrentData()
                                <div className="col-mx">
                                    The <strong>getCurrentData()</strong> method is used to get current data from current page
                                </div>
                            </li>
                            <li>getAllCurrentData()
                                <div className="col-mx">
                                    The <strong>getAllCurrentData()</strong> method is used to get all current data (include filter/search and order by)
                                </div>
                            </li>
                            <li>getCheckedData()
                                <div className="col-mx">
                                    The <strong>getCheckedData()</strong> method is used to get all checked data
                                </div>
                            </li>
                            <li>clearChecked()
                                <div className="col-mx">
                                    The <strong>clearChecked()</strong> method used to clear all checked input
                                </div>
                            </li>
                            <li>nextPage()
                                <div className="col-mx">
                                    The <strong>nextPage()</strong> method used to change to the next page of data
                                </div>
                            </li>
                            <li>prevPage()
                                <div className="col-mx">
                                    The <strong>prevPage()</strong> method used to change to the previous page of data
                                </div>
                            </li>
                            <li>pages()
                                <div className="col-mx">
                                    The <strong>prevPage()</strong> method used to get:
                                    <ul>
                                        <li>max_page : integer</li>
                                        <li>per_page : integer</li>
                                        <li>current_page : integer</li>
                                    </ul>
                                </div>
                            </li>
                            <li>jumpTo()
                                <div className="col-mx">
                                    The <strong>jumpTo()</strong> method used to change or jump to spesific a pages<br/>
                                    It take one parameter:
                                    <ul>
                                        <li>number of page : integer</li>
                                    </ul>
                                </div>
                            </li>
                        </ul>
                        <h3>Example</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>

                        <h3>Output:</h3>
                        <strong>Show Data</strong>
                        <div className="hg-50 ov-a col-mx">
                            <pre id="show-data"></pre>
                        </div>

                        <div className="col-mx">
                            <button id="clear-checked">Clear Checked</button>
                            <button id="get-data">Get Data</button>
                            <button id="get-checked">Get Checked Data</button>
                            <button id="get-current">Get Current Data</button>
                            <button id="get-all-current">Get All Current Data</button>
                        </div>
                        <div className="col-mx">
                            <button id="prev-page">Previous Page</button>
                            <button id="next-page">Next Page</button>
                        </div>
                        <div className="col-mx">
                            <button id="get-pages">Pages</button>
                        </div>
                        <div className="col-mx">
                            <input type="number" id="jump-input" className="p-v" placeholder="Number of pages ..."/>
                            <button id="jump-page">Jump</button>
                        </div>
                        <div className="col-mx">
                            <span>Grade</span>
                            <input type="number" className="p-v" placeholder="Find Grade ..." id="grades-input"/>
                        </div>

                        <div id="table-others"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsOthers;