import React from 'react';

class ArjunaneXamarinValidationRetrievingInput extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`valid.All();

//Or you can combine with Web Request

var form = webs.GetURL("your-url");
form.AddFields(valid.All());
webs.SendWebForm( (is_success, result, error) => {
    if(is_success)
    {
        // Your action or Serialized <strong>result</strong> JSON
    }
    else
    {
        // Handle the Error or DisplayAlert("Error", error, "OK");
    }
});`,
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "retrieving-input" ? "block" : "none"}}>
                   <h3>Retrieving Input</h3>
                    <div className="col-mx">
                        <span>You may also retrieve all of the input data as an Dictionary&lt;string, string&gt; using the <strong>All</strong> method:</span>
                    </div>
                    <pre className="syntax">{this.syntax[0]}</pre>
                    <div className="col-mx">
                        <strong>NOTE:</strong> the method <strong>All</strong> take one parameter (boolean and the default is <strong>false</strong>), when you call <strong>RunOnlyKeys</strong> or <strong>RunExceptKeys</strong> and you want get all input even if you call thats method, just set it to <strong>true</strong>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinValidationRetrievingInput;