import { useEffect } from "react";

class Helper {
    uriSegment(index) {
        var url = window.location.href.split('/');
        var gets = [];
        for(var i = 3; i < url.length; i++) {
            gets.push(url[i])
        }
        return typeof gets[index] !== 'undefined' && gets[index] !== "" ? gets[index] : null;
    }
    // uri Hash segment ("#")
    uriHashSegment(index) {
        var hash = window.location.href.split('#');
        var gets = [];
        if(hash.length === 1) return null;
        var url = hash[1].split("/");
        for(var i = 1; i < url.length; i++) {
            gets.push(url[i])
        }
        return typeof gets[index] !== 'undefined' && gets[index] !== "" ? gets[index] : null;
    }

    appendScript(src, callback) {
        const script = document.createElement("script");

        script.src = src;
        script.type = 'text/javascript';
        script.async = true;

        if(typeof callback !== 'undefined' && typeof callback === 'function') script.onload = () => callback(); 

        document.body.appendChild(script);

        document.body.removeChild(script);
    }

    appendCSS(src, callback) {
        var head  = document.getElementsByTagName('head')[0];
        var links = document.getElementsByTagName("link");
        var isFind = false;

        if(links.length > 0) {
            for(var i = 0; i < links.length; i++)
            {
                var ini = links[i];
                if(ini.href.indexOf(src) !== -1) isFind = true;
            }
        }

        if(isFind) return;

        var link  = document.createElement('link');
        link.rel  = 'stylesheet';
        link.type = 'text/css';
        link.href = src;
        link.media = 'all';
        head.appendChild(link);

        if(typeof callback !== 'undefined' && typeof callback === 'function') link.onload = () => callback(); 
        
    }

    setUrl(path) {
        return window.location.origin + "/" + path;
    }

    getUrlPath(href) {
        let l = document.createElement("a");
        l.href = href;
        return l.pathname;
    }
}

var helper = new Helper();
export default helper;