import React from 'react';
import photo from './../../../../assets/documentations/alertsLoading.jpg';

class ArjunaneFlutterAlertsLoading extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`showLoadingDialog(
    context     : context,
    title       : "Loading",
    color       : FlatColors.googleBlack,
    dismissible : false,
    message     : "Processing ...",

    // optional
    cancelAction: () => SnackBar(content: Text("Loading dialog is closed")),
    cancelText  : "Cancel/Batal"
);`,

`showLoadingDialog({
    @required BuildContext context,
    String title = "Loading", String message = "Sedang diproses ...", 
    Color color = FlatColors.googleBlue, bool dismissible = false,
    Function cancelAction, String cancelText
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "alerts-loading" ? "block" : "none"}}>
                    <h1>Alerts</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>showAlertDialog</strong> method displays a dialog box with a specified message, along with an OK and a Cancel button.</span>
                        </div>
                        <div className="col-mx">
                            <span>A confirm box is often used if you want the user to verify or accept something.</span>
                        </div>
                        <h3>showConfirmDialog</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>cancelAction</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is tapped or otherwise activated. This callback is to close the Loading Alert
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>canceText</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display cancelAction button's text
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>color</strong> &rarr; <span className="fc-b">Color</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">FlatColors.googleBlue</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>dismissible</strong> &rarr; <span className="fc-b">bool</span>
                                    </div>
                                    <div className="col-mx">
                                        If it set to <strong>true</strong>, this means that the user must tap a anywhere outside of the box to exit the Alert Dialog
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">false</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>message</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The text to display
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>title</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The title to display
                                    </div>
                                </li>
                            </ul>

                            <h3>Output:</h3>
                            <div className="img col-md-6">
                                <img src={photo} alt="Arjunane Alert Loading"/>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterAlertsLoading;