import React from 'react';

class ArjunaneFlutterSnackbar extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`snackBar(
    context, 
    "This is a light Snackbar it out!", 
    style: TypeStyle.info
)`,

`snackBar(BuildContext context, String text, {
    String title, 
    List<Widget> actions,
    TypeStyle style = TypeStyle.light,
    Widget Function(_SnackBarHelper) child,
    int milliseconds = 5000
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "snackbar" ? "block" : "none"}}>
                    <h1>Snack Bar</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            It can be useful to briefly inform your users when certain actions take place. For example, when a user swipes away a message in a list, you might want to inform them that the message has been deleted. You might even want to give them an option to undo the action.
                        </div>
                        <h3>snackBar</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>text</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The text to display
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>title</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text of tile to display
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>actions</strong> &rarr; <span className="fc-b">int</span>
                                    </div>
                                    <div className="col-mx">
                                        The (optional) set of actions that are displayed at the bottom of the dialog..
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>child</strong> &rarr; <span className="fc-b">Widget Function(_AlertContainerHelper)</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback to override content of <strong>alertContainer</strong> and get current of style (TypeStyle) from <strong>_AlertContainerHelper</strong>
                                    </div>
                                    <div className="col-mx">
                                        The <strong>_AlertContainerHelper</strong> constant:
                                    </div>
                                    <div className="col-mx">
                                        <ul>
                                            <li>backgroundColor &rarr; <span className="fc-b">Color</span></li>
                                            <li>dividerColor &rarr; <span className="fc-b">Color</span></li>
                                            <li>textColor &rarr; <span className="fc-b">Color</span></li>
                                        </ul>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>milliseconds</strong> &rarr; <span className="fc-b">Widget</span>
                                    </div>
                                    <div className="col-mx">
                                        The number of milliseconds to wait before executing the code.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">5000</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>style</strong> &rarr; <span className="fc-b">TypeStyle</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">TypeStyle.primary</i>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterSnackbar;