import React from 'react';
import {Route, Switch, withRouter} from 'react-router-dom';
import ArjunaneTableV2Container from './arjunane-table-v2/at_container';
import DocumentationHome from './home';
import "./../../assets/js/svg";
import helper from '../../helper/helper';
import ArjunaneFlutterContainer from './arjunane-flutter/at_container';
import ArjunaneUnityContainer from './arjunane-unity/at_container';
import ArjunaneXamarinContainer from './arjunane-xamarin/at_container';
import ArjunaneSharingan from './arjunane-sharingan/at_container';
import ArjunaneTableV1Container from './arjunane-table-v1/at_container';
import ArjunaneDatePickerV1Container from './arjunane-date-picker-v1/at_container';
import ArjunaneDatePickerV2Container from './arjunane-date-picker-v2/at_container';

class DocumentationContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = 
        {
            title : helper.uriHashSegment(1) !== null ? "|| " + helper.uriHashSegment(1).replace(/-/g," ").toUpperCase() : ""
        };
        
        this.unlisten = this.props.history.listen((location, action) => {
            this.setState({title: helper.uriHashSegment(1) !== null ? "|| " + helper.uriHashSegment(1).replace(/-/g," ").toUpperCase() : ""});
        });
    }

    componentWillUnmount() {
        this.unlisten();
    }

    componentDidMount() {
        let maxDate = 2021;
        let dateDOM = document.getElementById("date-year");
        let date    = new Date().getFullYear();
        let strDate = ``;
        if(date > maxDate) strDate = `${maxDate}-${date}`;
        else strDate = `${maxDate}`;
        dateDOM.innerHTML = strDate;
    }

    render() {
        return (
            <React.Fragment>
                <header class="header">
                    <a href="#/documentation" class="home-icon">
                        <img src={"/icons/ard.png"} alt="Arjunane || Dimas Awang Kusuma"/>
                        <span>ARJUNANE DOCS {this.state.title}</span>
                    </a>
                    {
                        this.state.title !== "" ? 
                        <div class="home-right">
                            <a href="#/documentation">Home</a>
                        </div>
                        : ""
                    }
                    
                </header>
                <Switch>
                    <Route exact path="/documentation">
                        <DocumentationHome/>
                    </Route>
                    
                    <Route path="/documentation/arjunane-flutter">
                        <ArjunaneFlutterContainer/>
                    </Route>
                    
                    <Route path="/documentation/arjunane-table-v1">
                        <ArjunaneTableV1Container/>
                    </Route>
                    
                    <Route path="/documentation/arjunane-table-v2">
                        <ArjunaneTableV2Container/>
                    </Route>
                    
                    <Route path="/documentation/arjunane-date-picker-v2">
                        <ArjunaneDatePickerV2Container/>
                    </Route>
                    
                    <Route path="/documentation/arjunane-date-picker-v1">
                        <ArjunaneDatePickerV1Container/>
                    </Route>
                    
                    <Route path="/documentation/arjunane-unity">
                        <ArjunaneUnityContainer/>
                    </Route>

                    <Route path="/documentation/arjunane-xamarin">
                        <ArjunaneXamarinContainer/>
                    </Route>

                    <Route path="/documentation/arjunane-sharingan">
                        <ArjunaneSharingan/>
                    </Route>
                </Switch>
                
                <footer class="footer">
                    <div class="col-mx cf">
                        <div class="col-md-4f">
                            <strong>Arjunane</strong>
                            <ul>
                                <li><a href="./">Portfolio</a></li>
                            </ul>
                        </div>
                        {
                            this.state.title !== "" ? 
                            <div class="col-md-4f">
                                <strong>Documentations</strong>
                                <ul>
                                    <li><a href="#documentation">Home</a></li>
                                </ul>
                            </div>
                            : ""
                        }
                    </div>
                    <div class="col-mx center">
                        <span>Copyright  <span id="date-year"></span> Arjunane.</span>
                    </div>
                </footer>
            </React.Fragment>
        );
    }
}

export default withRouter(DocumentationContainer);