import React from "react";
import modelSVG from "./ModelSVG";

class ModelMain extends React.Component {

    constructor(props) {
        super(props);

        this.svg            = modelSVG;

        this.cardContent    = document.getElementsByClassName("card-content");
        this.cardTitle      = document.getElementById("--title");

        this.idIcons        = [ "--facebook", "--github", "--name", "--birth", "--email", "--address"];
    }

    changePage(page) {

        this.removeActive();
        if(page == "profile") 
        {
            setTimeout(() => {
                this.cardContent[1].classList.add("active");
            }, 500);
        }
        else if(page == "portfolio") 
        {
            setTimeout(() => {
                this.cardContent[2].classList.add("active");
            }, 500);
        }
        else if(page == "docs") 
        {
            setTimeout(() => {
                this.cardContent[3].classList.add("active");
            }, 500);
        }
        else
        {
            setTimeout(() => {
                this.cardContent[0].classList.add("active");
            }, 500);
        }

        this.changeTitle(page);
    }

    setIcons() {
        this.idIcons.forEach( (val) => {
            var id = val.replace("--", "");
            document.getElementById(val).innerHTML = this.svg.initSVG()[id];
        });
    }

    changeTitle(title) {
        if(title !== 'profile' && title !== 'portfolio' && title !== 'docs') title = "WELCOME";
        else title = title.toUpperCase();
        
        var cardTitle = this.cardTitle;
        cardTitle.parentElement.classList.remove("active");
        setTimeout( function () {
            cardTitle.innerHTML = title;
            cardTitle.parentElement.classList.add("active");
        }, 500);
    }

    removeActive() {
        for(var i = 0; i < this.cardContent.length; i++) {
            this.cardContent[i].classList.remove("active");
        }
    }
}

var modelMain = new ModelMain();

export default modelMain;