import React from 'react';

class ArjunaneTableV2DocsHome extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
            `<script src="arjunane_table.js"></script> 
// OR 
<script src="arjunane_table.min.js"></script>`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "" || this.props.uri === null ? "block" : "none"}}>
                    <h1>Welcome to Arjunane Table documentation</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <strong>Arjunane Table</strong> is a simple script for processing data inside Table tags
                        </div>
                        <strong>Required : </strong>
                        <ul>
                            <li>arjunane_table.js <strong>OR</strong> arjunane_table.min.js</li>
                        </ul>
                        
                        <h3>Installation</h3>
                        <pre className="syntax">$ git clone https://github.com/dimas-ak/arjunane-table.git</pre>

                        <h3>Example</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>

                        <div className="col-mx">
                            <h3 className="dis-ib">NOTE:</h3> Please put <strong>arjunane_table.min.css</strong> (Arjunane CSS) in the same directory (folder)
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsHome;