import React from 'react';

class ArjunaneFlutterInitMain extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`void main() {
  WidgetsFlutterBinding.ensureInitialized();

  RoutersService.initialRouteSettings((_GetRoute route) async {

    // SharedPreferences shared = await SharedPreferences.getInstance();
    // route.baseRoute = shared.containsKey("isLogin") ? "home" : "login";
    
  });
}`,

`SharedPreferences shared = await SharedPreferences.getInstance();
route.baseRoute = shared.containsKey("isLogin") ? "home" : "login";`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "init-main" ? "block" : "none"}}>
                    <h1>Main</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            Similar with many object oriented programming languages, a main class or a file in a program shall most probably be the starting point of execution of the code. And similar is the case with flutter as well. The main.dart file consists of a single method void main(), which shall be invoked on boot.
                        </div>
                        <div className="col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        <h3>initialRouteSettings</h3>
                        <div className="col-mx">
                            Inside the void <span className="fc-b">main()</span> method we call the library method <span className="fc-b">runApp()</span> which shall load the app layout and all the experience on the screen.
                        </div>
                        <div className="col-mx">
                            In <strong>initialRouteSettings</strong> you can passing an argument to change the <strong>baseRoute</strong> on first load App.
                        </div>
                        <pre className="syntax">{this.syntax[1]}</pre>
                        <h3>_GetRoute</h3>
                        <div className="coll-mx">
                            <strong>Property</strong>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>baseRoute</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        Replace <strong>baseRoute</strong> from config.
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterInitMain;