import React from 'react';

class ArjunaneTableV2DocsDeleteData extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var deleteArTable = arTable.init("#table-delete", {
    ...
});

document.getElementById("delete").addEventListener("click", function () { 
    deleteArTable.deleteData(5); // it will delete from internal caches of data where the index is 5
});

// OR

document.getElementById("delete").addEventListener("click", function () { 
    var keys = [10, 15];
    deleteArTable.deleteData(keys);
});`,

`var deleteArTable = arTable.init("#table-delete", {
    url     : "json.json",
    title   : "Data Orang",
    checkable: true,
    thead   : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"],
    action  : [
        {
            text    : "Delete",
            cls     : "btn t1 red",
            href    : "json/update.json",
            listener: function  (data, event, element)
            {
                event.preventDefault();
                var url = element.getAttribute("href");
                arTable.req(url, function (res) {
                    if(res.error === null && arTable.isJSON(res.result))
                    {
                        var json = JSON.parse(res.result);
                        if(json.hasil)
                        {
                            deleteArTable.deleteData(data.key);
                        }
                    }
                    else
                    {
                        // Error
                    }
                });
            }
        }
    ]
    content_top :
    { 
        action : 
        [
            {
                text    : "Delete Selected",
                type    : "success",
                href    : "json/update-multiple.json",
                listener: function (data, event, element)
                {
                    event.preventDefault();
                    if(data.length > 0)
                    {
                        arTable.req(this.href, function (res) {
                            if(res.error === null && arTable.isJSON(res.result))
                            {
                                var json = JSON.parse(res.result);
                                var keys = new Array();
                                for(var i = 0; i < data.length; i++)
                                {
                                    keys.push(data[i].key);
                                }
                                if(json.hasil)
                                {
                                    deleteArTable.deleteData(keys);
                                }
                            }
                            else
                            {
                                // Error
                            }
                        });
                    }
                    else
                    {
                        alert("Please, select at least one row");
                    }
                }
            }
        ]
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "delete-data" ? "block" : "none"}}>
                    <h1>Delete Data</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            Method of <strong>deleteData</strong> is for deleting data into table's internal caches of data.
                        </div>
                        <div className="col-mx">
                            <strong>deleteData</strong> take one parameter
                            <ul>
                                <li>key : integer or array of integer</li>
                            </ul>
                        </div>
                        <div className="col-mx">
                            <strong>Example:</strong>
                        </div>
                        <div className="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>

                        <h3>Example Deleting</h3>
                        <div className="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <div id="table-delete"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsDeleteData;