import React from 'react';

class ArjunaneTableV2DocsOn extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var onArTable = arTable.init("#table-on", {
    url : "sample-json/json.json",
    title: "Data Orang",
    thead: ["ID", "name", "Grade", "Passing", "Sex", "Birth", "Place"],
    insert_column: [
        {
            html : function (data)
            {
                return '&lt;span> Grade : &lt;/span> &lt;input class="grade-input" value="' + data[2] + '">';
            }
        },
        {
            html: function(data)
            {
                return "&lt;button>BUTTON&lt;/button>";
            }
        },

    ]
});

onArTable.on("complete", function () {
    // Your action
    console.log("complete");
});
onArTable.on("change", function () {
    // Your action
    console.log("change");
});
onArTable.on("row", function (parent, data, index_current_row) {
    var button = parent.querySelector("button");
    var input = parent.querySelector(".grade-input");
    if(button !== null)
    {
        button.addEventlistener("click", function () {
            var msg = "value : " + input.value + "&lt;br>";
                msg += "key : " + data.key + "&lt;br>";
                msg += "data : " + data.data + "&lt;br>";
                msg += "index current row : " + index_current_row;
            onArTable.showAlert({
                title : "INFO",
                text : msg,
                button_text: "OK"
            });
        });
    }
});
onArTable.on("tbody", function (parent) {
    // Your action
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "on" ? "block" : "none"}}>
                   <h1>On</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>on</strong> method is to detect every update of table
                        </div>
                        <div className="col-mx">
                            <span>It takes 2 parameters :</span>
                            <ul>
                                <li>event : string</li>
                                <li>callback : method</li>
                            </ul>
                        </div>
                        <div className="col-mx">
                            <strong>Event list</strong>
                            <ul>
                                <li>complete : when getting the data is finished (without error), <strong>complete</strong> event will fired</li>
                                <li>change : when Next Page, Previous Page, Showing, Filter, Jump Page, Create, Update and Delete methods are fired, this event will fired</li>
                                <li>page_change : when Next Page, Previous Page and Jump Page methods are fired, this event will fired</li>
                                <li>next_page : when Next Page method is fired, this event will fired</li>
                                <li>prev_page : when Previous Page method is fired, this event will fired</li>
                                <li>showing : when Showing (per page) method is fired, this event will fired</li>
                                <li>filter : when filter method is fired, this event will fired</li>
                                <li>jump_page : when Jump Page method is fired, this event will fired</li>
                                <li>row
                                    <ul>
                                        <li>parent : Element of <strong>tr</strong></li>
                                        <li>data : Data of current row
                                            <ul>
                                                <li>key : get current index of data</li>
                                                <li>data : get current data</li>
                                            </ul>
                                        </li>
                                        <li>index_current_row : Index of current row (<strong>tr</strong>)</li>
                                    </ul>
                                </li>
                                <li>tbody
                                    <ul>
                                        <li>parent : Element of <strong>tbody</strong></li>
                                    </ul>
                                </li>
                            </ul>
                        </div>

                        <h3>Exmaple:</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        <div id="table-on"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsOn;