import React from 'react';
import modelDocumentation from '../../model/ModelDocumentation';

class DocumentationHome extends React.Component {

    constructor() {
        super();

        this.documentationsDOM = new Array();
        modelDocumentation.getDocs().forEach( (val) => {
            this.documentationsDOM.push(this.setDocs(val));
        });
    }

    setDocs(data) {
        var languages = data.language.split(",");
        return <div class="card-doc col-12"> 
                    <header class="card-title">
                        <strong>GET STARTED</strong>
                    </header>
                    <div class="col-mx">
                        <strong>{data.title}</strong>
                    </div>
                    <div class="col-mx">
                        <span>Language:</span>
                    </div>
                    <div class="col-mx">
                        {languages.map( (val) => {
                            
                           return   <div class="card-category">
                                        <span>{val}</span>
                                    </div>
                        })}
                    </div>
                    <div class="col-mx cf">
                        <div class="right">
                            <a target="_blank" href={data.github}>DOWNLOAD [GITHUB]</a>
                            <a href={"/" + data.docs}>DOCUMENTATION</a>
                        </div>
                    </div>
                    <img class="img" src={process.env.PUBLIC_URL + "/icons/" + data.icon}/>
                </div>;
    }

    render() {
        return <React.Fragment>
                    <div class="wellcome">
                        <h1 class="wellcome-title">
                            WELLCOME TO ARJUNANE DOCUMENTATION
                        </h1>
                    </div>

                    <div class="container center p-b-3">
                        <h1>Arjunane Documentations</h1>
                        
                        <section class="col-md-6 m-a" id="container-docs">
                            {this.documentationsDOM}
                        </section>
                    
                    </div>

                    <section class="bg-white p-5 center">
                        <h1>ABOUT</h1>
                        <div class="col-mx">
                            <span>Arjunane Library is a Micro-Library and Open Source for free.</span>
                        </div>
                        <div class="col-mx">
                            <span>I built some of these Mico-Libraries just to make it easier to work on the project I'm working on.</span>
                        </div>
                    </section>
                </React.Fragment>;
    }
}

export default DocumentationHome;