import React, { Fragment } from 'react';
import myPhoto from './../../assets/photo/crops.jpg';

import MainRightHome from './home.jsx';
import MainRightProfile from './profile.jsx';

import {withRouter, useHistory} from 'react-router-dom';
import NavLeft from './nav_left';
import MainRightProjects from './projects';
import modelMain from '../../model/ModelMain';
import MainRightDocumentations from './documentations';
import MainRightProjectDetail from './project_detail';
import helper from '../../helper/helper';

class ContainerMain extends React.Component {

    state = {
        className :"",
    };

    constructor(props) {
        super(props);

        this.state = 
        {
            className   : this.props.uri,
            detailData  : null,
            
            loadingIsLoading    : " is-loading",
            loadingActive       : " active",
            loadingIsShow       : " is-show"     
        };

        this.navLeftClicked = this.navLeftClicked.bind(this);
        this.detailClicked  = this.detailClicked.bind(this);
        this.detailBack     = this.detailBack.bind(this);
    }
    
    componentDidMount() {

        helper.appendScript(process.env.PUBLIC_URL + "/js/dragable/arjunane_dragable.js", () => {
            window.arDragable.init(".img-preview");
        });
        modelMain.cardTitle = document.getElementById("--title");
        modelMain.setIcons();
        modelMain.changeTitle(this.state.className);
    }

    navLeftClicked(e, page) {
        modelMain.changePage(page.state.activeClass);
        this.routingFunction("/" + page.state.activeClass);
        // this.setRightActivePage(page.state.activeClass);
    }
    
    routingFunction = (url) => {
        this.props.history.push({
            pathname: url,
            state: url
        });
    }

    setRightActivePage = (e) => {
        this.setState({className: e});
        return e;
    }

    isInitProjectsDone = () => {
        // menghentikan putaran kotak
        setTimeout( () => {
            this.setState({loadingIsLoading : ""});
        }, 500);
        // memisahkan 4 kotak
        setTimeout(() => {
            this.setState({loadingActive : ""});
        }, 1500);
        // membuat z-index menjadi -1
        setTimeout( () => {
            this.setState({loadingIsShow : ""});
        }, 2500);
    }

    detailClicked(data)
    {
        this.setState({detailData : data});
    }

    detailBack()
    {
        this.setState({detailData : null});
    }

    render() {
        return <Fragment>
            <div className="background cf">
                    <div className="col-container">
                        <div className="col-left">
                            <div className="img-pp img">
                                <img src={myPhoto} alt="Dimas Awang Kusuma || Arjunane" />
                            </div>
                            <div className="col-mx center">
                                <strong>Dimas Awang Kusuma</strong>
                            </div>
                            <div className="col-mx col-icon center">
                                <a href="https://www.facebook.com/arjunane.co.id" id="--facebook" target="_blank">

                                </a>
                                <a href="https://github.com/dimas-ak" id="--github" target="_blank">

                                </a>
                            </div>
                            <div className="wrap">
                                <div className="nav-wrap">
                                    <div className="nav-content">
                                        <NavLeft onClicked={this.navLeftClicked} setActive={this.state.className}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-right">
                            <div className="card cf">
                                <div className="card-title active">
                                    <h3 id="--title">WELCOME</h3>
                                </div>
                                <div className="card-wrap">
                                    <MainRightHome nameClass={this.state.className}/>
                                    <MainRightProfile nameClass={this.state.className}/>
                                    <MainRightProjects isDone={this.isInitProjectsDone} nameClass={this.state.className} isDetailClicked={this.detailClicked}/>
                                    <MainRightDocumentations nameClass={this.state.className}/>
                                    {/* <Route path="/">
                                        <MainLeftHome nameClass={this.state.className}/>
                                    </Route>
                                    <Route path="/profile">
                                        <MainLeftProfile nameClass={this.state.className}/>
                                    </Route> */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <MainRightProjectDetail onBackPress={this.detailBack} ref={instance => { 
                    this.child = instance; 
                    if(this.child !== null) this.child.setDetail(this.state.detailData);
                }}/>
                
                <div class={`loading${this.state.loadingIsShow}${this.state.loadingActive}${this.state.loadingIsLoading}`}>
                    <div class="loading-left-top">
                        <img src={process.env.PUBLIC_URL + "icons/main-icon.png"} alt="Dimas Awang Kusuma || Arjunane"/>
                    </div>
                    <div class="loading-left-bottom">
                        <img src={process.env.PUBLIC_URL + "icons/main-icon.png"} alt="Dimas Awang Kusuma || Arjunane"/>
                    </div>
                    <div class="loading-right-top">
                        <img src={process.env.PUBLIC_URL + "icons/main-icon.png"} alt="Dimas Awang Kusuma || Arjunane"/>
                    </div>
                    <div class="loading-right-bottom">
                        <img src={process.env.PUBLIC_URL + "icons/main-icon.png"} alt="Dimas Awang Kusuma || Arjunane"/>
                    </div>
                    <div class="loading-box"></div>
                </div>
        </Fragment>;
    
    }
}

export default withRouter(ContainerMain);