import React from 'react';

class ArjunaneXamarinWebGetUrl extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`using ArjunaneXamarin;
namespace tamVan
{
    [XamlCompilation(XamlCompilationOptions.Compile)]
    public partial class TamVanSekali : ContentPage
    {
        ArjunaneWebRequest webs = new ArjunaneWebRequest();
    }
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "get-url" ? "block" : "none"}}>
                    <h3>Get URL</h3>
                    <div class="col-mx p-l-3">
                        <strong>GetURL()</strong> is for set URL to Request.<br/>
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>String</li>
                                </ul>
                            </li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">webs.GetURL("https://your-url.com");</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinWebGetUrl;