import React from 'react';

class ArjunaneTableV2DocsInsertColumn extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var insertColumnArTable = arTable.init("#insert-column", {
    ...
    insert_column: [
        {
            thead   : "Median",
            value   : function (data)
            {
                return data[2] / 2;
            },
            html : function (data)
            {
                return data[7] >= 30 ? '&lt;span style="color:green">' + data[7] + '&lt;/span>' : '&lt;span style="color:red">' + data[7] + '&lt;/span>';
            }
        },
        {
            html: function(data)
            {
                return data[7] >= 30 ? "More than 30" : "Less than 30";
            }
        }
    ]
});`,

`var insertColumnArTable = arTable.init("#insert-column", {
    ...
    insert_column: [
        {
            thead   : "Median",
            key     : "median",
            value   : function (data)
            {
                return data["grade"] / 2;
            },
            html : function (data)
            {
                return data["grade"] >= 30 ? '&lt;span style="color:green">' + data[7] + '&lt;/span>' : '&lt;span style="color:red">' + data[7] + '&lt;/span>';
            }
        },
        {
            key : "type",
            html: function(data)
            {
                return data["median"] >= 30 ? "More than 30" : "Less than 30";
            }
        }
    ]
});`,

`var insertColumnArTable = arTable.init("#insert-column", {
    ...
});

document.getElementById("btn-insert-column").addEventListener("click", function () { 
    insertColumnArTable.insertColumn([
        {
            thead   : "Median",
            value   : function (currentRow)
            {
                return currentRow[2] / 2;
            },
            html : function (currentRow)
            {
                return currentRow[7] >= 30 ? '&lt;span style="color:green">' + currentRow[7] + '&lt;/span>' : '&lt;span style="color:red">' + currentRow[7] + '&lt;/span>';
            }
        },
        {
            html: function(currentRow)
            {
                return currentRow[7] >= 30 ? "More than 30" : "Less than 30";
            }
        }
    ]);
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "insert-column" ? "block" : "none"}}>
                    <h1>Insert Column</h1>
                    <div class="p-x">
                        <div class="col-mx">
                            Method of <strong>insert_column</strong> or <strong>insertColumn</strong> is for inserting data into table's internal caches of data.
                        </div>
                        <div class="col-mx">
                            <strong>insert_column</strong> or <strong>insertColumn</strong> take 4 parameters
                            <ul>
                                <li>thead : string</li>
                                <li>key : integer or string (Only for Array of Objects)</li>
                                <li>value : function/method</li>
                                <li>html : function/method with return to <strong>string</strong></li>
                            </ul>
                        </div>
                        <h3>NOTE</h3>
                        <ul>
                            <li><span>The property <strong>key</strong> is required if your data is <strong>Array of Objects</strong> and you don't need to put property key if your data is <strong>Array</strong></span></li>
                            <li><span>When your data is <strong>Array</strong>, it will increment your index key.</span></li>
                            <li><span>When your property <strong>key</strong> is exist on current data, it will throw <strong>error</strong></span></li>
                            <li>The property <strong>value</strong> is for inserting to local caches of data</li>
                            <li>The property <strong>html</strong> is output from <strong>value</strong></li>
                            <li>When you use <strong>insertColumn</strong> method, it will remove all your recent searches data from input or others</li>
                        </ul>
                        <div class="col-mx">
                            <strong>Example for Array Data:</strong>
                        </div>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>

                        <h3>Example for Array of Objects Data</h3>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>
                        <h3>Example for insertColumn method:</h3>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[2]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <button id="btn-insert-column">Insert Column</button>
                        <div id="table-insert-column"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsInsertColumn;