import React from 'react';

class ArjunaneUnityLoadImage extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var image = ar.Get("RawImage_user").GameObject;

StartCoroutine(
    ar.GetURL("your-image-url").LoadImageRequest(image)
);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "load-image" ? "block" : "none"}}>
                  <h1>Load Image for RawImage</h1>
                  <div className="col-mx">
                        <div className="bg-w2 p-x">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityLoadImage;