
import React from 'react';

class ArjunaneTableV1DocsCheckableAction extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`checkable: true,
action_checkable: 
[
    {
        cls     : "add",
        url     : "add/",
        text    : "TAMBAH BERITA",
    },
    {
        cls     : "delete",
        url     : "Scripts/delete.php",
        text    : "HAPUS",
        params  : [0],
        isRemove: true,
        field   : 0,
        msg     : "Do you really want to delete all of these?",
        col_msg : "{i} = {m}"
    },
    {
        cls     : "change",
        url     : "Scripts/change.php",
        text    : "-CHANGE-",
        params  : [0],
        field   : 0,
        changeData: 
        {
            changeIndex : [1, 2] // merubah data text pada index td
        },
        msg     : "Do you really want to change all of these?",
        col_msg : "{i} = {m}"
    }
]

/*
<i>
    <h3>Change</h3>
    Let's say, you have <strong>change.php</strong> and then the output is:
    $json = [ "My Name 7", 90];
    echo json_encode($json);
    Then, all of <strong>td</strong> for <strong>Name</strong> and <strong>Grade</strong> (which are checked )will be changed (including if you have <strong>array_info</strong> on it).
    <strong>NOTE</strong> : All <strong>td</strong> will be changed into <strong>My Name 7</strong> and <strong>90</strong>.
    <h3>Delete</h3>
    Let's say, you have <strong>delete.php</strong> and then the output is:
    $json["result"] = true;
    echo json_encode($json);
    Then, all of <strong>tr</strong> and the data (which are checked) will be removed.
    <strong>NOTE</strong> : That the <strong>"result"</strong> of json( whatever is <strong>false</strong> or <strong>true</strong>), it will true except it have an error from the server.
    <h3>NOTE</h3>
    - That my script can't modify for your json file (Except you have backend to changing it).
    - You need to run XAMPP or WAMPP, etc. Because delete.php and change.php need <strong>PhpMyAdmin</strong>
</i>
*/`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "checkable-action" ? "block" : "none"}}>
                   <h1>Tutorials : 7.Checkable Action(s)</h1>
                    <div className="col-mx">
                        <ul>
                            <li>checkable : BOOLEAN (<strong>required*</strong>)</li>
                            <li>checkable_action :</li>
                            <ul>
                                <li>cls         : STRING</li>
                                <li>url         : STRING</li>
                                <li>params      : ARRAY =&gt; INT</li>
                                <li>text        : STRING</li>
                                <li>isRemove    : BOOLEAN (remove data/tr)</li>
                                <li>params      : ARRAY =&gt; INT</li>
                                <li>field       : INT (for &#123;i&#125; message)</li>
                                <li>msg         : STRING 
                                    <ul>
                                        <li>&#123;i&#125; : index from <strong>field</strong> (thead text)</li>
                                        <li>&#123;m&#125; : index from <strong>td</strong></li>
                                    </ul>
                                </li>
                                <li>changeData  : Object
                                    <ul>
                                        <li>changeIndex : ARRAY =&gt; INT (index of <strong>td</strong>)</li>
                                    </ul>
                                </li>
                            </ul>
                        </ul>
                    </div>
                    <div className="col-mx bg-w2 p-x">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV1DocsCheckableAction;