import React from 'react';

class ArjunaneDatePickerV1Home extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`kalender.dom('.date').ex({
    days: 
    {
        'Minggu':'Min',
        'Senin':'Sen',
        'Selasa':'Sel',
        'Rabu':'Rab',
        'Kamis':'Kam',
        "Jum'at":'Jum',
        'Sabtu':'Sab'
    },
    format: 'yyyy-mm-dd',
    time: true,
    year_range: '1995:2020' // OR year_range: "2020"
});`,

`days: 
{
    'Minggu': 'Ming', 
    'Senin': 'Sen', 
    'Selasa': 'Sel', 
    'Rabu': 'Rab', 
    'Kamis': 'Kam', 
    "Jum'at": 'Jum', 
    'Sabtu': 'Sab'
},

months : 
[
    'Januari', 
    'Februari', 
    'Maret', 
    'April', 
    'Mei', 
    'Juni', 
    'Juli', 
    'Agustus', 
    'September', 
    'Oktober', 
    'November', 
    'Desember'
],

format: 'yyyy-mm-dd',

time : false`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "" || this.props.uri === null ? "block" : "none"}}>
                    <div className="col-mx center">
                        <h1>-=[ Arjunane-datepicker.js v1 DEMO ]=-</h1>
                        <div className="col-mx">
                            <span>Dependency-free Javascript library Date Picker v1</span>
                        </div>
                    </div>

                    <div className="col-mx">
                        <strong>Required:</strong>
                        <ul>
                            <li>arjunane-date.css</li>
                            <li>arjunane-date.js</li>
                        </ul>
                    </div>
                    
                    <div className="col-mx">
                        <strong>Simple init</strong>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                        <div className="col-mx">
                            <strong>Output:</strong>
                            <div className="f-input">
                                <span>Date</span>
                                <input type="text" className="date"/>
                            </div>
                        </div>
                        <div className="col-mx">
                            <strong>Default:</strong>
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[1]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV1Home;