import React from 'react';

class ArjunaneUnitySet extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`ar.Get("Panels").Animator(); // get component Animator and return it
ar.Get("Username_field").InputField(); // get component Input Field and return it
ar.Get("User_RawImage").RawImage(); // get component Raw Image and return it
ar.Get("User_text").Text(); // get component Text and return it`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "set" ? "block" : "none"}}>
                   <h1>Set</h1>

                    <div className="col-mx">
                        <div className="col-mx">
                            <span>Method <strong>Set</strong> is used to Get Component of the Game Object</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnitySet;