import React from 'react';

class ArjunaneUnityForEach extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var panels = ar.Get("Panel");
panels.ForEach( (go, i) => {
    GameObject gameObject = go;
    int index = i;
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "foreach" ? "block" : "none"}}>
                    <h1>ForEach</h1>

                    <div className="col-mx">
                        <div className="col-mx">
                            <span>Looping your GameObjects</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityForEach;