import React from 'react';

class ArjunaneFlutterCommandsConfig extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`dart arjunane create:controller mencoba`,

`dart arjunane create:controller c_home --all
Enter View class Name : v_home // input command
Enter Model class Name : m_home // input command`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "commands-config" ? "block" : "none"}}>
                    <h1>Commands Config class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            It provides a number of helpful commands for your use while developing your application.
                        </div>
                        <div className="col-mx">
                            <h3>commandsAssign</h3>
                            <div className="col-mx">
                                When you craete a new command, then you must include it into <strong>commandsAssign</strong> property.
                            </div>

                            <h3>insertClassNameLast</h3>
                            <div className="col-mx">
                                When it set to <strong>true</strong> the suffix for the names of the files from the result of the generate command will end in accordance with the command.
                            </div>
                            <div className="col-mx">
                                For example:
                                <pre className="syntax-global">{this.syntax[0]}</pre>
                                The output of file will : <strong>mencoba_controller.dart</strong>
                            </div>
                            <div className="col-mx">
                                For example:
                                <pre className="syntax-global">{this.syntax[1]}</pre>
                                <div className="col-mx">
                                    The output of Controller will : <strong>c_home_controller.dart</strong>
                                </div>
                                <div className="col-mx">
                                    The output of View will : <strong>v_home_view.dart</strong>
                                </div>
                                <div className="col-mx">
                                    The output of Model will : <strong>m_home_model.dart</strong>
                                </div>
                            </div>

                            <h3>showErrorInternal</h3>
                            <div className="col-mx">
                                Set it to true, then the error of internal command will showing up if there's not working correctly.
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterCommandsConfig;