import React from 'react';

class ArjunaneTableV2DocsUpdateData extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var updateArTable = arTable.init("#table-update", {
    ...
});

document.getElementById("update").addEventListener("click", function () { 
    var update = [ 101, "Arjuna", 70, 1, "Male", "2020-05-10", "Pekajangan"];
    updateArTable.updateData(1, update); // this will update the second index
});`,

`var updateArTable = arTable.init("#table-insert", {
    ...
});

document.getElementById("update").addEventListener("click", function () { 
    var update = {
        "id"    : 101,
        "name"  : "Arjunane",
        "grade" : 70,
        "passing": 1,
        "birth" : "2020-05-10",
        "place" : "Pekajangan"
    };
    updateArTable.updateData(1, update); // this will update the second index
});`,

`var update =  { 
    0 : 101,  // ID
    2 : 70,  // Grade
    6 : "Pekajangan" // Place
};
updateArTable.updateData(1, update);

// <strong>OR</strong>

var update =  { 
    "id"    : 101,  // ID
    "grade" : 70,  // Grade
    "place" : "Pekajangan" // Place
};
updateArTable.updateData(1, update);`,

`var update =  [
    { 
        0 : 101,  // ID
        2 : 70,  // Grade
        6 : "Pekajangan" // Place
    },
    { 
        0 : 50,  // ID
        2 : 69,  // Grade
        6 : "Pekalongan" // Place
    }
];
var keys = [ 10, 15];
updateArTable.updateData(keys, update);

// <strong>OR</strong>

var update =  [
    { 
        "id"    : 101,  // ID
        "grade" : 70,  // Grade
        "place" : "Pekajangan" // Place
    },
    { 
        "id"    : 50,  // ID
        "grade" : 69,  // Grade
        "place" : "Pekalongan" // Place
    }
];
var keys = [ 10, 15];
updateArTable.updateData(keys, update);`,

`var updateArTable = arTable.init("#table-update", {
    url     : "json.json",
    title   : "Data Orang",
    checkable: true,
    thead   : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"],
    action  : [
        {
            text    : "Edit specific",
            cls     : "btn t1 blue",
            href    : "json/update.json",
            listener: function  (data, event, element)
            {
                event.preventDefault();
                var url = element.getAttribute("href");
                
                    /*
                        output of res.result is :
                        {
                            "0" : 101,
                            "1" : "Arjunane tamVan",
                            "6" : "Kajen"
                        }
                    */
                
                arTable.req(url, function (res) {
                    if(res.error === null && arTable.isJSON(res.result))
                    {
                        updateArTable.updateData(data.key, JSON.parse(res.result));
                    }
                    else
                    {
                        // Error
                    }
                });
            }
        }
    ]
    content_top :
    { 
        action : 
        [
            {
                text    : "Update 2 data of Selected",
                type    : "success",
                href    : "json/update-multiple.json",
                listener: function (data, event, element)
                {
                    event.preventDefault();
                    if(data.length > 0)
                    {
                        arTable.req(this.href, function (res) {
                            if(res.error === null && arTable.isJSON(res.result))
                            {
                                var keys = new Array();
                                for(var i = 0; i < data.length; i++)
                                {
                                    keys.push(data[i].key);
                                }
                                updateArTable.updateData(keys, JSON.parse(res.result));
                            }
                            else
                            {
                                // Error
                            }
                        });
                    }
                    else
                    {
                        alert("Please, select at least one row");
                    }
                }
            }
        ]
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "update-data" ? "block" : "none"}}>
                    <h1>Update Data</h1>
                    <div class="p-x">
                        <div class="col-mx">
                            Method of <strong>updateData</strong> is for updating data from table's internal caches of data.
                        </div>
                        <div class="col-mx">
                            <h3 class="dis-ib">NOTE:</h3> Please to always be consistent in using JSON, either <strong>Array</strong> or <strong>Array of Object</strong>
                        </div>
                        
                        <strong>updateData</strong> takes 2 parameters:
                        <ul>
                            <li>key:
                                <ul>
                                    <li>integer or array of integer</li>
                                </ul>
                            </li>
                            <li>
                                data
                                <ul>
                                    <li>array or array of object</li>
                                </ul>
                            </li>
                        </ul>
                        <h3>Update Array</h3>
                        <div class="col-mx">
                            <span>If your table's internal caches of data is <strong>Array</strong> (also from <strong>init</strong> method to replace your table tag info Arjunane Table)</span>
                        </div>
                        <div class="col-mx">
                            <strong>Example:</strong>
                        </div>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>

                        <h3>Update Array of Object</h3>
                        <div class="col-mx">
                            <span>If your table's internal caches of data is <strong>Array of Object</strong></span>
                        </div>
                        <div class="col-mx">
                            <strong>Example:</strong>
                        </div>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>

                        <h3>Update for specific row</h3>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[2]}</pre>
                        </div>

                        <h3>Update for specific multiple row</h3>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[3]}</pre>
                        </div>

                        <h3>Example Updating</h3>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[4]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <div id="table-update"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsUpdateData;