import React from 'react';
import modelDocumentation from '../../model/ModelDocumentation';

class MainRightDocumentations extends React.Component {

    constructor(props) {
        super(props);

        this.documentations = new Array();

        var newArray = new Array();
        modelDocumentation.getDocs().forEach( (val) => {
            this.documentations.push(this.setDocumentation(val));
        });
    }

    setDocumentation(data) {
        return <div class="col-mx card2 m-t-3 cf"> 
                    <div class="img">
                        <img src={ process.env.PUBLIC_URL + "icons/" + data.icon} alt={data.title}/>
                    </div>
                    <div class="card2-content">
                        <h2>{data.title}</h2>
                        <div class="col-mx">
                            <span>{data.subTitle}</span>
                        </div>
                        <div class="col-mx">
                            <a class="btn green t4" href={data.github} target="_blank">
                                <span>DOWNLOAD/GITHUB</span>
                            </a>
                            <a class="btn blue t3" href={data.docs} target="_blank">
                                <span>DOCUMENTATION</span>
                            </a>
                        </div>
                    </div>
                </div>;
    }

    render() {
        var nameClass = this.props.nameClass;
        return (<div className={"card-content" + ( nameClass === "docs" ? " active" : "")}>
                    <div class="col-mx center m-b-3">
                        <h2>Documentations</h2>
                    </div>
                    <hr/>
                    <div class="p-x" id="docs">
                        {this.documentations}
                    </div>
                </div>
        );
    }
}

export default MainRightDocumentations;