import React from 'react';
import photo from './../../../../assets/documentations/alertsContainer.jpg';

class ArjunaneFlutterAlertContainer extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`AlertsContainer("This is a danger alertcheck it out!", 
    title: "This is title", 
    style: TypeStyle.danger
)

AlertsContainer("This is a danger alertcheck it out!", 
    title: "This is title", 
    style: TypeStyle.danger
),

AlertsContainer(null, child: (colors) {
    return Column(
      children: [
        Text("This is a CUSTOM dark alertcheck it out!", style: TextStyle(color: colors.textColor))
      ],
    );
}, style: TypeStyle.dark)`,
`AlertsContainer(
    String text, {
    String title, TypeStyle style = TypeStyle.primary, Widget Function(_AlertContainerHelper) child
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "alert-container" ? "block" : "none"}}>
                    <h1>Alerts</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>alertContainer</strong> element represents a container for introductory content or a set of navigational links</span>
                        </div>
                        
                        <h3>alertContainer</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            
                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>text</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>child</strong> &rarr; <span className="fc-b">Widget Function(_AlertContainerHelper)</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback to override content of <strong>alertContainer</strong> and get current of style (TypeStyle) from <strong>_AlertContainerHelper</strong>
                                    </div>
                                    <div className="col-mx">
                                        The <strong>_AlertContainerHelper</strong> constant:
                                    </div>
                                    <div className="col-mx">
                                        <ul>
                                            <li>backgroundColor &rarr; <span className="fc-b">Color</span></li>
                                            <li>borderColor &rarr; <span className="fc-b">Color</span></li>
                                            <li>textColor &rarr; <span className="fc-b">Color</span></li>
                                        </ul>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>style</strong> &rarr; <span className="fc-b">TypeStyle</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">TypeStyle.primary</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>title</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display.
                                    </div>
                                </li>
                            </ul>

                            <h3>Output:</h3>
                            <div className="img col-md-6">
                                <img src={photo}/>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterAlertContainer;