import React from 'react';

class ArjunaneUnitySendInputPost extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`InputField username = ar.Get("input_username").InputField();
InputField password = ar.Get("input_password").InputField();

var form = ar.GetURL("your_url").AddField("username", username.text)
                        .AddField("password", password.text);

// OR
Dictionary&lt;string, string> dr = new Dictionary &lt;string, string>();
dr.Add("username", username.text);
dr.Add("password", password.text);
var form = ar.GetURL("your_url").AddFields(dr);

// Send it to your Website
StartCoroutine( form.SendForm( (result, is_success, error) => {
    if(is_success)
    {
        // your action
    }
    else
    {
    // Error : your handled
    }
}) );

SendForm( () => {
    string result,
    bool is_success,
    string error,
    int time_out // ( = Default is 60 in second(s) )
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "send-input-post" ? "block" : "none"}}>
                  <h1>Send Input Post</h1>
                    <div className="col-mx">
                        <div className="bg-w2 p-x">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnitySendInputPost;