import React from 'react';

class ArjunaneDatePickerV2DaysName extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default
daysName : 
[
    ["Minggu", "Min"], 
    ['Senin', "Sen"], 
    ["Selasa", "Sel"], 
    ["Rabu", "Rab"], 
    ["Kamis", "Kam"], 
    ["Jumat", "Jum"], 
    ["Sabtu", "Sab"]
]`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "days-name" ? "block" : "none"}}>
                    <h3 id="daysname">Days Name</h3>
                    <div className="col-mx">
                        <span>type : <i>ARRAY</i> =&gt; STRING, STRING</span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2DaysName;