import firebase from 'firebase/app';
import React from 'react';

import 'firebase/analytics';
import 'firebase/database';

class InitFirebase extends React.Component {

    constructor(props) {
        super(props);

        // TODO: Replace the following with your app's Firebase project configuration
        // For Firebase JavaScript SDK v7.20.0 and later, `measurementId` is an optional field
        const firebaseConfig = {
            apiKey: "AIzaSyC0_OirsqE4zXXTHy98gAKHzw4LTSvmleg",
            authDomain: "arjunane-app.firebaseapp.com",
            databaseURL: "https://arjunane-app.firebaseio.com",
            projectId: "arjunane-app",
            storageBucket: "arjunane-app.appspot.com",
            messagingSenderId: "391958661338",
            appId: "1:391958661338:web:a14f93735eaf85bec9fde3"
        };
        
        // Initialize Firebase
        let initConfig;
        if (!firebase.apps.length) {
            initConfig = firebase.initializeApp(firebaseConfig);
         }else {
            initConfig = firebase.app(); // if already initialized, use that one
         }

        this.state = {
            dataFirebase : new Array()
        };
        initConfig.database().ref("projects").off();
        initConfig.database().ref("projects").once( "value", (_items) => {
            var items = _items.val();
            items.sort(function compare(a, b) {
                var dateA = new Date(a.date);
                var dateB = new Date(b.date);
                var calc = dateB - dateA;
                return calc;
            });

            var newArray = new Array();
            for (var i = 0; i < items.length; i++) {
                var ini = items[i];
                ini.photo = this.initPhoto(ini.photo);
        
                var photos = new Array();
                for (var p = 0; p < ini.photos.length; p++) {
                    var photo = ini.photos[p];
                    photos.push(this.initPhoto(photo));
                }
                ini.css = ini.icon.split(".")[0];
                ini.icon = (ini.icon === 'codeigniter.png' ? 'php.png' : ini.icon);
        
                ini.photos = photos;
                
                newArray.push(this.setProject(ini));
            }
            
            this.setState({
                dataFirebase : newArray
            });
        });

        this.props.isDone();

    }

    initPhoto(photo) {
        return `https://firebasestorage.googleapis.com/v0/b/arjunane-app.appspot.com/o/projects%2F${photo}?alt=media`;
    }

    setProject(data) {
        return  <div className={"project " + data.css} tag={data.tag} onClick={() => {this.props.projectClicked(data);}}>
                    <div className="project-image">
                        <img src={data.photo}/>
                    </div>
                    <div className="project-content cf">
                        <div className="icon-category img">
                            <img src={process.env.PUBLIC_URL + "icons/" + data.icon}/>
                        </div>
                        <div className="project-wrap">
                            <div class="">
                                <strong>{data.title_en}</strong>
                            </div>
                            <div className="col-mx">
                                <i>{data.date}</i>
                            </div>
                        </div>
                    </div>
                </div>;
    }

    render() {
        return (this.state.dataFirebase);
    }

}

export default InitFirebase;