import React from 'react';
import {Route, Switch} from 'react-router-dom';
import helper from '../../../helper/helper';
import ArjunaneLeft from '../container_left';
import ArjunaneFlutterHome from './docs/af_1_home';
import ArjunaneFlutterConfig from './docs/af_2_config';
import ArjunaneFlutterController from './docs/af_3_controller';
import ArjunaneFlutterModel from './docs/af_4_model';
import ArjunaneFlutterView from './docs/af_5_view';
import ArjunaneFlutterViewExtends from './docs/af_6_view_extends';
import ArjunaneFlutterAlerts from './docs/af_9_alerts';
import ArjunaneFlutterAlertsConfirm from './docs/af_10_alerts_confirm';
import ArjunaneFlutterAlertsLoading from './docs/af_11_alerts_loading';
import ArjunaneFlutterAlertContainer from './docs/af_7_alert_container';
import ArjunaneFlutterAlertNotifier from './docs/af_8_alert_notifier';
import ArjunaneFlutterButton from './docs/af_12_button';
import ArjunaneFlutterButtonOutline from './docs/af_13_button_outline';
import ArjunaneFlutterSnackbar from './docs/af_14_snackbar';
import ArjunaneFlutterStandards from './docs/af_15_standards';
import ArjunaneFlutterTypeAlertDialog from './docs/af_16_type_alert_dialog';
import ArjunaneFlutterTypeStyle from './docs/af_17_type_style';
import ArjunaneFlutterFlashData from './docs/af_19_flashdata';
import ArjunaneFlutterFlatColors from './docs/af_20_flat_colors';
import ArjunaneFlutterFontSize from './docs/af_21_font_size';
import ArjunaneFlutterHelper from './docs/af_23_helper';
import ArjunaneFlutterForms from './docs/af_22_forms';
import ArjunaneFlutterFileServer from './docs/af_18_file_server';
import ArjunaneFlutterValidation from './docs/af_24_validation';
import ArjunaneFlutterRedirect from './docs/af_25_redirect';
import ArjunaneFlutterRequest from './docs/af_26_request';
import ArjunaneFlutterRoutersAnimation from './docs/af_27_routers_animation';
import ArjunaneFlutterCommandsConfig from './docs/af_28_commands_config';
import ArjunaneFlutterCommands from './docs/af_29_commands';
import ArjunaneFlutterOnBackPress from './docs/af_30_on_back_press';
import ArjunaneFlutterInitMain from './docs/af_31_init_main';
import ArjunaneFlutterCloseDialog from './docs/af_11_5_close_dialog';

class ArjunaneFlutterContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'dart');
            window.arSyntax.init(".syntax-global", 'global');
        });

        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-flutter" navClick={this.navigationsClick}/>
                    
                    <ArjunaneFlutterHome uri={this.state.uri}/>
                    <ArjunaneFlutterConfig uri={this.state.uri}/>
                    <ArjunaneFlutterController uri={this.state.uri}/>
                    <ArjunaneFlutterModel uri={this.state.uri}/>
                    <ArjunaneFlutterView uri={this.state.uri}/>
                    <ArjunaneFlutterViewExtends uri={this.state.uri}/>
                    <ArjunaneFlutterAlertContainer uri={this.state.uri}/>
                    <ArjunaneFlutterAlertNotifier uri={this.state.uri}/>
                    <ArjunaneFlutterAlerts uri={this.state.uri}/>
                    <ArjunaneFlutterAlertsConfirm uri={this.state.uri}/>
                    <ArjunaneFlutterAlertsLoading uri={this.state.uri}/>
                    <ArjunaneFlutterCloseDialog uri={this.state.uri}/>
                    <ArjunaneFlutterButton uri={this.state.uri}/>
                    <ArjunaneFlutterButtonOutline uri={this.state.uri}/>
                    <ArjunaneFlutterSnackbar uri={this.state.uri}/>
                    <ArjunaneFlutterStandards uri={this.state.uri}/>
                    <ArjunaneFlutterTypeAlertDialog uri={this.state.uri}/>
                    <ArjunaneFlutterTypeStyle uri={this.state.uri}/>
                    <ArjunaneFlutterFileServer uri={this.state.uri}/>
                    <ArjunaneFlutterFlashData uri={this.state.uri}/>
                    <ArjunaneFlutterFlatColors uri={this.state.uri}/>
                    <ArjunaneFlutterFontSize uri={this.state.uri}/>
                    <ArjunaneFlutterForms uri={this.state.uri}/>
                    <ArjunaneFlutterHelper uri={this.state.uri}/>
                    <ArjunaneFlutterValidation uri={this.state.uri}/>
                    <ArjunaneFlutterRedirect uri={this.state.uri}/>
                    <ArjunaneFlutterRequest uri={this.state.uri}/>
                    <ArjunaneFlutterRoutersAnimation uri={this.state.uri}/>
                    <ArjunaneFlutterCommandsConfig uri={this.state.uri}/>
                    <ArjunaneFlutterCommands uri={this.state.uri}/>
                    <ArjunaneFlutterOnBackPress uri={this.state.uri}/>
                    <ArjunaneFlutterInitMain uri={this.state.uri}/>

                </React.Fragment>;
    }
}

export default ArjunaneFlutterContainer;