import logo from './logo.svg';
import './App.css';
import helper from "./helper/helper.js";
import ContainerMain from './view/main/container.jsx';
import { BrowserRouter as Router, HashRouter, Route } from "react-router-dom";
import DocumentationContainer from './view/documentation/container';
import './assets/css/arjunane.min.css';

function Init() {
    require('./assets/css/portfolio.min.css');
    require('./assets/css/documentation.min.css');
}

function App() {
    var uri = helper.uriHashSegment(0);
    Init();
    return (
        <HashRouter>
            {
                uri !== 'documentation' ?
                    <ContainerMain uri={uri}/>
                 :
                     <DocumentationContainer/>
            }
        </HashRouter>
    );
}

export default App;
