import React from 'react';

class ArjunaneFlutterOnBackPress extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`class CHomePage extends Pages {
    @override
    State<StatefulWidget> createState() => CHomeController();
  
    @override
    Future<bool> onBackPress(BuildContext context) async {
        
      bool confirm = await showConfirmDialog(context: context, 
                      message: "Are you sure want to exit?");
      
      return confirm;
    }
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "on-back-press" ? "block" : "none"}}>
                    <h1>On Back Press method</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            Registers a callback to veto attempts by the user to dismiss the enclosing ModalRoute.
                        </div>
                        <div className="col-mx">
                            The <strong>onBackPress</strong> method using <span className="fc-b">WillPopScope</span> class.
                        </div>
                        <div className="col-mx">
                            <strong className="fc-r">NOTE</strong> : The <strong>onBackPress</strong> method only available in <strong>Pages</strong>.
                        </div>
                        <div className="col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterOnBackPress;