import React from 'react';

class ArjunaneXamarinValidationFails extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`if(valid.Fails())
{
    // Error
}
else
{
    // Success
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "fails" ? "block" : "none"}}>
                    <h3>Fails</h3>
                    <div className="col-mx">
                        <span><strong>Fails</strong> method is like <a href="#run"><strong>Run</strong>, <strong>RunOnlyKeys</strong> or <strong>RunExceptKeys</strong></a> method, it will checking that if the validations is succeeded or not.</span>
                    </div>
                    <pre className="syntax">{this.syntax[0]}</pre>
                    <div className="col-mx">
                        <strong>NOTE:</strong> <span>Method <strong>Fails</strong> will be executed if the method Run are triggered.</span>
                    </div>

                
                </div>;
    }
}

export default ArjunaneXamarinValidationFails;