import React from 'react';

class ArjunaneXamarinWebSendWebForm extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`webs.GetURL("https://your-url.com");
webs.AddField("username", entry_username.Text);
webs.AddField("password", entry_password.Text);
webs.SendWebForm( (is_success, result, error) => {
    if(is_success)
    {
        // Your action or Serialized <strong>result</strong> JSON
    }
    else
    {
        // Handle the Error or DisplayAlert("Error", error, "OK");
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "send-web-form" ? "block" : "none"}}>
                    <h3>Send Web Form</h3>
                    <div className="col-mx p-l-3">
                        <strong>SendWebForm()</strong> is for sending the Request form.<br/>
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>Action
                                        <ul>
                                            <li>Boolean</li>
                                            <li>String</li>
                                            <li>String</li>
                                        </ul>
                                    </li>
                                    <li>Integer (Timeout) : Default is 30 seconds</li>
                                    <li>String (Method) : Default is "POST"</li>
                                    <li>String (Content Type) : Default is "application/x-www-form-urlencoded"</li>
                                </ul>
                            </li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinWebSendWebForm;