import React from 'react';

class ArjunaneUnityIndexOf extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`int index = 5;
var panels = ar.Get("Panel");
panels.IndexOf(index).GameObject;`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "index-of" ? "block" : "none"}}>
                   <h1>Index Of</h1>

                    <div className="col-mx">
                        <div className="col-mx">
                            <span>Get a GameObject for certain index</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityIndexOf;