import React from 'react';

class ArjunaneDatePickerV2FormatDate extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default : "mm-dd-yyyy"
// Another formatDate :
"mm-dd-yyyy", // bulan-hari-tahun
"mm-yyyy-dd", // bulan-tahun-hari
"dd-mm-yyyy", // hari-bulan-tahun
"dd-yyyy-mm", // hari-tahun-bulan
"yyyy-mm-dd", // tahun-bulan-hari
"yyyy-dd-mm", // tahun-hari-bulan

"mm.dd.yyyy", // bulan.hari.tahun
"mm.yyyy.dd", // bulan.tahun.hari
"dd.mm.yyyy", // hari.bulan.tahun
"dd.yyyy.mm", // hari.tahun.bulan
"yyyy.mm.dd", // tahun.bulan.hari
"yyyy.dd.mm", // tahun.hari.bulan


"mm/dd/yyyy", // bulan/hari/tahun
"mm/yyyy/dd", // bulan/tahun/hari
"dd/mm/yyyy", // hari/bulan/tahun
"dd/yyyy/mm", // hari/tahun/bulan
"yyyy/mm/dd", // tahun/bulan/hari
"yyyy/dd/mm", // tahun/hari/bulan

// Example :
formatDate : "yyyy-mm-dd"`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "format-date" ? "block" : "none"}}>
                    <h3 id="formatdate">Format Date</h3>
                    <div className="col-mx">
                        <span>type : <i>STRING</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2FormatDate;