import React from 'react';

class ArjunaneDatePickerV2Attribute extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default attr : "value"
// Let's say, you want to put the DatePicker in DIV, then

// Example :
// attr : "value"`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "attribute" ? "block" : "none"}}>
                    <h3 id="attr">Attribute</h3>
                    <div className="col-mx">
                        <span>type : <i>STRING</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <h3>Output: </h3>
                        <div id="date-picker" contenteditable="" className="p-x" value="">Click Me</div>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2Attribute;