import React from 'react';

class ArjunaneUnityFind extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var panel = ar.Get("Panel");
panel.Find("Text_username");`,

`public class Example : MonoBehaviour
{
    public GameObject _parentGameObject;

    void Start()
    {
        var find = ar.Find(_parentGameObject, "Text_password");
    }
}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "find" ? "block" : "none"}}>
                    <h1>Find</h1>
                    <div className="col-mx">
                        <span>Find your GameObject(s):</span>
                    </div>
                    <div className="col-mx">
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                        <div className="col-mx">
                            <span>Set your GameObject(s) to find Your GameObject(s)</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[1]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityFind;