import React from 'react';

class ArjunaneTableV2DocsDeleteColumn extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var deleteColumnArTable = arTable.init("#insert-column", {
    ...
});

// <i>Example below for Array Data</i>
document.getElementById("btn-delete-column").addEventListener("click", function () { 
    deleteColumnArTable.deleteColumn([0, 1]);
});

// <i>Example below for Array of Objects Data</i>
document.getElementById("btn-delete-column").addEventListener("click", function () { 
    deleteColumnArTable.deleteColumn(["id", "sex"]);
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "delete-column" ? "block" : "none"}}>
                   <h1>Delete Column</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            Method of <strong>deleteColumn</strong> is for deleting data from table's internal caches of data.
                        </div>
                        <div className="col-mx">
                            <strong>insert_column</strong> or <strong>insertColumn</strong> take 1 parameter
                            <ul>
                                <li>Array of <strong>string</strong> or <strong>integer</strong></li>
                            </ul>
                        </div>
                        <h3>NOTE</h3>
                        <div className="col-mx">
                            <span>The method <strong>deleteColumn</strong> cannot delete hidden data/value</span><br/>
                            <span>When you use <strong>deleteColumn</strong> method, it will remove all your recent searches data from input or others</span>
                        </div>

                        <h3>Example for deleteColumn method:</h3>
                        <div className="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <button id="btn-delete-column">Delete Columns index 0 and 1</button>
                        <div id="table-delete-column"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsDeleteColumn;