import React from 'react';

class ArjunaneFlutterValidation extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`class CHomeController extends Controller<CHomePage> {
    
    Validation valid    = new Validation();
    Forms forms         = new Forms();
  
    void saveData() {
        if(forms.validate())
        {

        }
    }

    @override
    Widget build(BuildContext context) {
  
      // TODO: implement build
      return VHomeView(this);
    }
  }`,

`class VHomeView extends View<CHomeController> {

    VHomeView (CHomeController controller) : super(controller);
  
    @override
    Widget build(BuildContext context) {
      return Scaffold(
        appBar: AppBar(
          title: Text("Your Title")
        ),
        body: Container(
          child: controller.forms.formOpen(
            child: Column(
              children: [

                controller.forms.formInput('E-Mail', "email", 
                  placeholder: "Input your E-Mail ...", 
                  validator: (val) => controller.validation.setRules(val, "E-Mail", 'required|email', name: 'email'),
                  icon: Icons.email),
                  
                controller.forms.formInput('Password', "password", 
                  placeholder: "Input your Password ...", 
                  obscureText: true, 
                  validator: (val) => controller.validation.setRules(val, "Password", 'required|min_length:6|max_length:50', name: 'password'),
                  icon: Icons.vpn_key),

                Buttons('SUBMIT', onPressed: controller.saveData )
              ],
            )
          )
        ),
     );
  
    }
  
}`,

`@override
void initState() {
    valid.setErrorMessage("required", "The {field} is required.");
    super.initState();
}`,

`String setRules(
    String value, 
    String label, 
    String validations, 
    {
        String name, int indexForm = 0
    }
)`,

`String getText(String name, {int indexForm = 0})`,

`Map<String, String> all({
    List<String> only, 
    List<String> except, 
    int indexForm = 0
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "validation" ? "block" : "none"}}>
                    <h1>Validation class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            Arjunane provides a comprehensive data validation class that helps minimize the amount of code youll write..
                        </div>
                        <div className="col-mx">
                            <h3>Initialized</h3>
                            <pre className="syntax">{this.syntax[0]}</pre>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h3>setErrorMessage</h3>
                            <div className="col-mx">
                                If youd like to include a fields human name, or the optional parameter some rules allow for (such as max_length), you can add the 	&#123;field&#125; and &#123;param&#125; tags to your message, respectively:
                            </div>
                            <pre className="syntax">{this.syntax[2]}</pre>
                            <div className="col-mx">
                                On a field with the human name Username and a rule of min_length[5], an error would display: Username must have at least 5 characters.
                            </div>

                            <h3>setRules</h3>
                            <div className="col-mx">
                                Set as many validation rules as you need for a given field, cascading them in order, and it even lets you prep and pre-process the field data at the same time. To set validation rules you will use the set_rules() method:
                            </div>
                            <pre className="syntax">{this.syntax[3]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>value</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Get current value of <span className="fc-b">TextFormField</span> from <span className="fc-b">validator()</span>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>label</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A human name for this field, which will be inserted into the error message. For example, if your field is named user you might give it a human name of Username.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>validation</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The validation rules for this form field.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>name</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The field name - the exact name youve given the form field.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>indexForm</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        Set index of validation to your forms <strong>indexKey</strong>
                                    </div>
                                </li>
                            </ul>

                            <h3>getText</h3>
                            <div className="col-mx">
                                Get current value from <span className="fc-b">formInput</span>, <span className="fc-b">formDropdown</span>
                            </div>
                            <pre className="syntax">{this.syntax[4]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>name</strong> &rarr; <span className="fc-b">String</span>  <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field name - the exact name youve given the form field.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>indexForm</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        Set index of validation to your forms <strong>indexKey</strong>
                                    </div>
                                </li>
                            </ul>

                            <h3>all</h3>
                            <div className="col-mx">
                                The <span className="fc-b">all()</span> method will retrieve all inputs value in <span className="fc-b">Map&lt;String, String&gt;</span>.
                            </div>
                            <pre className="syntax">{this.syntax[5]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>only</strong> &rarr; <span className="fc-b">List&lt;String&gt;</span>
                                    </div>
                                    <div className="col-mx">
                                        Get current values with returns only the specified <strong>name</strong> pairs from the given array.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>except</strong> &rarr; <span className="fc-b">List&lt;String&gt;</span>
                                    </div>
                                    <div className="col-mx">
                                        Get current values with returns except the specified <strong>name</strong> pairs from the given array.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>indexForm</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        Set index of validation to your forms <strong>indexKey</strong>
                                    </div>
                                </li>
                            </ul>

                            <h3>Available Validation Rules</h3>
                            <div className="col-mx">Below is a list of all available validation rules and their function:</div>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>alpha</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be entirely alphabetic characters.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>alpha_dash</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation may have alpha characters, as well as dashes and underscores.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>alpha_numeric</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be entirely alpha-numeric characters.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>alpha_numeric_dash</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be entirely alpha-numeric characters, as well as dashes and underscores.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>boolean</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be able to be cast as a boolean. Accepted input are <span className="fc-r">true</span> and <span className="fc-r">false</span>.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>different:field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must have a different value than field (<i>name</i>).
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>email</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be formatted as an email address. 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>ends_with:tamvan,dan,berani</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must end with one of the given values.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>gt:field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be greater than the given field. The two fields must be of the same type.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>gte:field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be greater than or equal to the given field. The two fields must be of the same type.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>in:tamvan,dan,berani</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be included in the given list of values.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>integer</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must exist in anotherfield's values.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>ip</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be an IP address.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>ipv4</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be an IPv4 address.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>ipv6</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be an IPv6 address.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>json</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be a valid JSON string.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>lt:field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be less than the given field. The two fields must be of the same type.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>lte:field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be less than or equal to the given field. The two fields must be of the same type.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>max:value</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be less than or equal to a maximum value.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>min:value</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must have a minimum value.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>name</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must have a human name. For example : <span className="fc-r">Samuel Eto'o</span>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>not_in:tamvan,dan,berani</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must not be included in the given list of values.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>not_regex:pattern</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must not match the given regular expression.
                                    </div>
                                    <div className="col-mx">
                                        Internally, this rule uses the dart <span className="fc-r">RegExp</span> function. The pattern specified should obey the same formatting required by <span className="fc-r">RegExp</span> and thus also include valid delimiters.
                                    </div>
                                    <div className="col-mx">
                                        For example: <span className="fc-b">'not_regex:/^.+$/i'</span>
                                    </div>
                                    <div className="col-mx">
                                        <strong className="fc-r">NOTE</strong> : When using the <span className="fc-r">regex</span> / <span className="fc-r">not_regex</span> patterns, it may be necessary to specify your validation rules using an array instead of using <span className="fc-r">|</span> delimiters, especially if the regular expression contains a <span className="fc-r">|</span> character.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>nullable</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation may be <span className="fc-r">null</span>.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>numeric</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be numeric.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>not_regex:pattern</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must match the given regular expression.
                                    </div>
                                    <div className="col-mx">
                                        Internally, this rule uses the dart <span className="fc-r">RegExp</span> function. The pattern specified should obey the same formatting required by <span className="fc-r">RegExp</span> and thus also include valid delimiters.
                                    </div>
                                    <div className="col-mx">
                                        For example: <span className="fc-b">'regex:/^.+@.+$/i'</span>
                                    </div>
                                    <div className="col-mx">
                                        <strong className="fc-r">NOTE</strong> : When using the <span className="fc-r">regex</span> / <span className="fc-r">not_regex</span> patterns, it may be necessary to specify your validation rules using an array instead of using <span className="fc-r">|</span> delimiters, especially if the regular expression contains a <span className="fc-r">|</span> character.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be present in the input data and not empty.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>required_if:another_field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be present and not empty if the another_field field is equal to any value.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>same:field</strong>
                                    </div>
                                    <div className="col-mx">
                                        The given field must match the field under validation.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>starts_with:tamvan,dan,berani</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must start with one of the given values.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>url</strong>
                                    </div>
                                    <div className="col-mx">
                                        The field under validation must be a valid URL.
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterValidation;