import React from 'react';

class ArjunaneFlutterHome extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`git clone https://github.com/dimas-ak/arjunane-flutter.git`,

`flutter create myapp
cd myapp`,

`dart arjunane init`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "" || this.props.uri === null ? "block" : "none"}}>
                    <h1>Welcome to Arjunane Flutter documentation</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <strong>Arjunane Flutter</strong> is a simple script for building a mobile application
                        </div>
                        
                        <h3>Installation</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            Use the <span className="fc-r">flutter create</span> command to create a new project:
                        </div>
                        <pre className="syntax">{this.syntax[1]}</pre>
                        <div className="col-mx">
                            Copy all files inside <strong>arjunane-flutter</strong> folder that you already clone, then paste all of it into your project (In this example, paste in <strong>myapp</strong>).
                        </div>
                        <div className="col-mx">
                            And then <strong>type</strong> this command to get all packages that required for <strong>Arjunane Flutter</strong>.
                        </div>
                        <pre className="syntax">{this.syntax[2]}</pre>
                        <div className="col-mx">
                            <strong className="fc-r">NOTE</strong> : make sure that your directory command prompt was already in <strong>myapp</strong> directory.
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterHome;