import React from 'react';

class ArjunaneDatePickerV2MultipleDate extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`// Default  multiSetDate : false
// Default Separator is , OR comma

// Set it to true and press CTRL + Click
// Output :`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "multiple-date" ? "block" : "none"}}>
                    <h3 id="multiplesetdate">Multiple Date</h3>
                    <div className="col-mx">
                        <span>type : <i>BOOLEAN</i></span>
                    </div>
                    <div className="col-mx p-x bg-w2">
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="f-input">
                            <span>Multi Set Date</span>
                            <input type="text" className="multi" placeholder="Date ..."/>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneDatePickerV2MultipleDate;