import React from 'react';

class ArjunaneXamarinWebAddFields extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`Dictionary&lt;string, string> dict = new Dictionary&lt;string, string>();

dict.Add("username", entry_username.Text);
dict.Add("password", entry_password.Text);

webs.AddFields(dict);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "add-fields" ? "block" : "none"}}>
                    <h3>Add Fields</h3>
                    <div className="col-mx p-l-3">
                        <strong>AddFields()</strong> is for adding data to Request.<br/>
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>Dictionary &lt;string,string&gt;</li>
                                </ul>
                            </li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinWebAddFields;