import React from 'react';
import {Route, Switch} from 'react-router-dom';
import helper from '../../../helper/helper';
import ArjunaneUnityInsertGameObject from './docs/au_10_insert_gameobject';
import ArjunaneUnityClone from './docs/au_11_clone';
import ArjunaneUnityWeb from './docs/au_12_web';
import ArjunaneUnitySendInputPost from './docs/au_13_send_input-post';
import ArjunaneUnityLoadImage from './docs/au_14_load_image';

import ArjunaneUnityHome  from './docs/au_1_home';
import ArjunaneUnityGetGameObject  from './docs/au_2_get_gameobject';
import ArjunaneUnityFind from './docs/au_3_find';
import ArjunaneUnityClick from './docs/au_4_click';
import ArjunaneUnityForEach from './docs/au_5_foreach';
import ArjunaneUnitySetActive from './docs/au_6_set_active';
import ArjunaneUnityIndexOf from './docs/au_7_index_of';
import ArjunaneUnitySet from './docs/au_8_set';
import ArjunaneUnityColorFromHex from './docs/au_9_color_from_hex';
import ArjunaneLeft from './../container_left';

class ArjunaneUnityContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'c#');
        });

        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-unity" navClick={this.navigationsClick}/>
                    
                    <ArjunaneUnityHome uri={this.state.uri}/>
                    <ArjunaneUnityGetGameObject uri={this.state.uri}/>
                    <ArjunaneUnityFind uri={this.state.uri}/>
                    <ArjunaneUnityClick uri={this.state.uri}/>
                    <ArjunaneUnityForEach uri={this.state.uri}/>
                    <ArjunaneUnitySetActive uri={this.state.uri}/>
                    <ArjunaneUnityIndexOf uri={this.state.uri}/>
                    <ArjunaneUnitySet uri={this.state.uri}/>
                    <ArjunaneUnityColorFromHex uri={this.state.uri}/>
                    <ArjunaneUnityInsertGameObject uri={this.state.uri}/>
                    <ArjunaneUnityClone uri={this.state.uri}/>
                    <ArjunaneUnityWeb uri={this.state.uri}/>
                    <ArjunaneUnitySendInputPost uri={this.state.uri}/>
                    <ArjunaneUnityLoadImage uri={this.state.uri}/>

                </React.Fragment>;
    }
}

export default ArjunaneUnityContainer;