import React from 'react';

import photoDanger from './../../../../assets/documentations/alertsDanger.jpg';
import photoInfo from './../../../../assets/documentations/alertsInfo.jpg';
import photoSuccess from './../../../../assets/documentations/alertsSuccess.jpg';
import photoWarning from './../../../../assets/documentations/alertsWarning.jpg';

class ArjunaneFlutterAlerts extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`showAlertDialog(
    context         : context,
    message         : "Showing an Alert Dialog",
    typeAlertDialog : TypeAlertDialog.info,
    dismissible     : true,
    title           : "Info Alert",
    onOk            : () {
      // your action
    }
);`,
`showAlertDialog({
    @required BuildContext context, @required String message, 
    TypeAlertDialog typeAlertDialog = TypeAlertDialog.info, String title = "INFO", String textButton = "OK",
    bool dismissible = true, Function onOk
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "alerts" ? "block" : "none"}}>
                    <h1>Alerts</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>An alert dialog informs the user about situations that require acknowledgement. An alert dialog has an optional title and an optional list of actions. The title is displayed above the content and the actions are displayed below the content.</span>
                        </div>
                        
                        <h3>showAlertDialog</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            
                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>dismissible</strong> &rarr; <span className="fc-b">bool</span>
                                    </div>
                                    <div className="col-mx">
                                        If it set to <strong>false</strong>, this means that the user must tap a button to exit the Alert Dialog
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">true</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>message</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The text to display
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onOk</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is tapped or otherwise activated
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>textButton</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display button text
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Ok</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>title</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The title to display
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">INFO</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>typeAlertDialog</strong> &rarr; <span className="fc-b">TypeAlertDialog</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">TypeAlertDialog.info</i>
                                    </div>
                                </li>
                            </ul>

                            <h3>Output:</h3>
                            <div className="cf">
                                <div className="img col-md-6 left">
                                    <img src={photoDanger} alt="Arjunane Alert Danger"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photoInfo} alt="Arjunane Alert Info"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photoSuccess} alt="Arjunane Alert Success"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photoWarning} alt="Arjunane Alert Warning"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterAlerts;