import React from 'react';
import {Route, Switch} from 'react-router-dom';
import './../../../assets/css/documentation_xamarin.min.css';
import helper from '../../../helper/helper';
import ArjunaneXamarinHome from './docs/axa_1_home';
import ArjunaneXamarinGridCount from './docs/axa_2_grid_count';
import ArjunaneXamarinValidationOnInput from './docs/axv_10_on_input';
import ArjunaneXamarinValidationFormEnabled from './docs/axv_11_form_enabled';
import ArjunaneXamarinValidationFormResetValue from './docs/axv_12_form_reset_value';
import ArjunaneXamarinValidationFormReset from './docs/axv_13_form_reset';
import ArjunaneXamarinValidationOverrideErrorsMessages from './docs/axv_14_override_errors_message';
import ArjunaneXamarinValidationAvailableValidationRules from './docs/axv_15_available_validation_rules';
import ArjunaneXamarinValidation from './docs/axv_1_home';
import ArjunaneXamarinValidationSetRules from './docs/axv_2_set_rules';
import ArjunaneXamarinValidationSetErrorMessage from './docs/axv_3_set_error_message';
import ArjunaneXamarinValidationRuns from './docs/axv_4_runs';
import ArjunaneXamarinValidationFails from './docs/axv_5_fails';
import ArjunaneXamarinValidationErrors from './docs/axv_6_errors';
import ArjunaneXamarinValidationRetrievingInput from './docs/axv_7_retrieving_input';
import ArjunaneXamarinValidationInsertPicker from './docs/axv_8_insert_picker';
import ArjunaneXamarinValidationReplacePicker from './docs/axv_9_replace_picker';
import ArjunaneXamarinWebHome from './docs/axw_1_home';
import ArjunaneXamarinWebGetUrl from './docs/axw_2_get_url';
import ArjunaneXamarinWebAddField from './docs/axw_3_add_field';
import ArjunaneXamarinWebAddFields from './docs/axw_4_add_fields';
import ArjunaneXamarinWebAddFile from './docs/axw_5_add_file';
import ArjunaneXamarinWebSendWebRequest from './docs/axw_6_send_web_request';
import ArjunaneLeft from './../container_left';
import ArjunaneXamarinWebSendWebFormMultipart from './docs/axw_8_send_web_form_multipart';
import ArjunaneXamarinWebSendWebForm from './docs/axw_7_send_web_form';
import ArjunaneXamarinWebDownloadData from './docs/axw_9_download_data';

class ArjunaneXamarinContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'c#');
        });

        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-xamarin" navClick={this.navigationsClick}/>
                    
                    <ArjunaneXamarinHome uri={this.state.uri}/>
                    <ArjunaneXamarinGridCount uri={this.state.uri}/>

                    <ArjunaneXamarinValidation uri={this.state.uri}/>
                    <ArjunaneXamarinValidationSetRules uri={this.state.uri}/>
                    <ArjunaneXamarinValidationSetErrorMessage uri={this.state.uri}/>
                    <ArjunaneXamarinValidationRuns uri={this.state.uri}/>
                    <ArjunaneXamarinValidationFails uri={this.state.uri}/>
                    <ArjunaneXamarinValidationErrors uri={this.state.uri}/>
                    <ArjunaneXamarinValidationRetrievingInput uri={this.state.uri}/>
                    <ArjunaneXamarinValidationInsertPicker uri={this.state.uri}/>
                    <ArjunaneXamarinValidationReplacePicker uri={this.state.uri}/>
                    <ArjunaneXamarinValidationOnInput uri={this.state.uri}/>
                    <ArjunaneXamarinValidationFormEnabled uri={this.state.uri}/>
                    <ArjunaneXamarinValidationFormResetValue uri={this.state.uri}/>
                    <ArjunaneXamarinValidationFormReset uri={this.state.uri}/>
                    <ArjunaneXamarinValidationOverrideErrorsMessages uri={this.state.uri}/>
                    <ArjunaneXamarinValidationAvailableValidationRules uri={this.state.uri}/>

                    <ArjunaneXamarinWebHome uri={this.state.uri}/>
                    <ArjunaneXamarinWebGetUrl uri={this.state.uri}/>
                    <ArjunaneXamarinWebAddField uri={this.state.uri}/>
                    <ArjunaneXamarinWebAddFields uri={this.state.uri}/>
                    <ArjunaneXamarinWebAddFile uri={this.state.uri}/>
                    <ArjunaneXamarinWebSendWebRequest uri={this.state.uri}/>
                    <ArjunaneXamarinWebSendWebForm uri={this.state.uri}/>
                    <ArjunaneXamarinWebSendWebFormMultipart uri={this.state.uri}/>
                    <ArjunaneXamarinWebDownloadData uri={this.state.uri}/>
                </React.Fragment>;
    }
}

export default ArjunaneXamarinContainer;