import React from 'react';

class ArjunaneTableV2DocsShowConfirm extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var confirmArTable = arTable.init("#table-confirm", {
    ...
});

document.getElementById("confirm-button").addEventlistener("click", function () {
    confirmArTable.showConfirm({
        type        : "success",
        title       : "PERINGATAN",
        text        : "Are you sure want to delete this data?",
        accept_text : "Ok",
        abort_text  : "No"
    });
});`,

`var confirmArTable = arTable.init("#table-confirm", {
    url     : "json/json.json",
    title   : "Data Orang",
    checkable: true,
    thead   : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"],
    action  : 
    [
        {
            text    : "Delete",
            cls     : "btn t1 red",
            href    : "json/delete.json",
            listener: function(data, event, element) 
            {
                event.preventDefault();
                var name = data.data[1];
                confirmArTable.showConfirm({
                    type        : "danger",
                    title       : "WARNING",
                    text        : "Are you sure want to delete this data [&lt;strong>" + name + "&lt;/strong>]?",
                    accept_text : "Ok",
                    abort_text  : "No"
                }, function () {

                    var url = element.getAttribute("href");
                    arTable.req(url, function(res) {
                        if (res.error === null && arTable.isJSON(res.result)) 
                        {
                            var json = JSON.parse(res.result);
                            if (json.hasil) {
                                confirmArTable.deleteData(index);
                                confirmArTable.showAlert({
                                    type: "success",
                                    title: "BERHASIL",
                                    text: "Successfully deleted the data : " + name,
                                    button_text: "Ok"
                                });
                            }
                        } else {
                            // Error
                        }
                    });

                });
                
            }
        }
    ],
    content_top: 
    {
        action: 
        [
            {
                text    : "Delete Selected",
                type    : "success",
                href    : "json/delete.json",
                listener: function(data, event, element) {
                    event.preventDefault();
                    var url = element.getAttribute("href");
                    if (data.length > 0) 
                    {
                        confirmArTable.showConfirm({
                            type        : "danger",
                            title       : "WARNING",
                            text        : "Are you sure want to delete these selected data?",
                            accept_text : "Ok",
                            abort_text  : "No"
                        }, function () {
                            arTable.req(url, function(res) {
                                if (res.error === null && arTable.isJSON(res.result)) 
                                {
                                    var json = JSON.parse(res.result);
                                    if (json.hasil) {
                                        for(var i = 0; i < data.length; i++)
                                        {
                                            confirmArTable.deleteData(data[i].key)
                                        }
                                        confirmArTable.showAlert({
                                            type: "success",
                                            title: "BERHASIL",
                                            text: "Successfully deleted the data",
                                            button_text: "Ok"
                                        });
                                    }
                                } else {
                                    // Error
                                }
                            });
        
                        });
                        
                    } else {
                        confirmArTable.showAlert({
                            type: "danger",
                            title: "Ops",
                            text: "Please at least choose one row",
                            button_text: "Ok"
                        });
                    }
                }
            }
        ]
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "show-confirm" ? "block" : "none"}}>
                   <h1>Show Confirm</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>showConfirm</strong> method displays a dialog box with a specified message, along with an OK and a Cancel button.</span>
                        </div>
                        <div className="col-mx">
                            <span>A confirm box is often used if you want the user to verify or accept something.</span>
                        </div>
                        <div className="col-mx">
                            <strong>showConfirm</strong> takes 3 arguments:
                            <ul>
                                <li>Object
                                    <ul>
                                        <li>
                                            type
                                            <ul>
                                                <li>warning</li>
                                                <li>info (<strong>Default</strong>)</li>
                                                <li>success</li>
                                                <li>danger</li>
                                            </ul>
                                        </li>
                                        <li>title   : string (Default is : "INFO")</li>
                                        <li>text    : string (Default is : "Info Confirm")</li>
                                        <li>accept_text : string (Default is : "Yes")</li>
                                        <li>abort_text : string (Default is : "No")</li>
                                    </ul>
                                </li>
                                <li>Callback Accept : action method</li>
                                <li>Callback Abort : action method</li>
                            </ul>
                        </div>
                        
                        <h3>Example</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>

                        <h3>Example Show Confirm</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>

                        <h3>Output:</h3>
                        <div id="table-confirm"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsShowConfirm;