import React from 'react';

class ArjunaneXamarinValidationReplacePicker extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`list.Clear();
list.Add("Male");
list.Add("Female");

valid.ReplaceFormPickerList("gender", list);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "replace-picker" ? "block" : "none"}}>
                    <h3>Replace Form List Picker</h3>
                    <div className="col-mx">
                        <span>This method will changing (Replaced) the value of the <strong>Picker</strong> that you have been set previously.</span>
                    </div>
                    <pre className="syntax">{this.syntax[0]}</pre>
                    <div className="col-mx">
                        <span>The above method takes two parameters:</span>
                        <ul>
                            <li>The field name or key that you've set in <strong>SetRules</strong>.</li>
                            <li>Your new list.</li>
                        </ul>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinValidationReplacePicker;