import React from 'react';

class ArjunaneXamarinWebAddField extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`webs.AddField("username", entry_username.Text);
webs.AddField("password", entry_password.Text);`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "add-field" ? "block" : "none"}}>
                    <h3>Add Field</h3>
                    <div className="col-mx p-l-3">
                        <strong>AddField()</strong> is for adding data to Request.<br/>
                        <ul>
                            <li>Parameter
                                <ul>
                                    <li>String</li>
                                    <li>String</li>
                                </ul>
                            </li>
                        </ul>
                        <strong>Example :</strong>
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinWebAddField;