import React from 'react';

class ArjunaneFlutterView extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`dart arjunane create:view v_home c_home`,

`import 'package:flutter/material.dart';
import '../controller/c_home_controller.dart';
import '../../system/arjunane.dart';

class VHomeView extends View<CHomeController> {

  VHomeView (CHomeController controller) : super(controller);

  @override
  Widget build(BuildContext context) {

    return Scaffold(
      appBar: AppBar(
        title: Text("Your Title")
      ),
      body: Container(
        // child: your child
      ),
   );

  }

}`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "view" ? "block" : "none"}}>
                    <h1>View class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            A <strong>View</strong> is simply a mobile page, or a page fragment, navigations, drawers, etc. In fact, views can flexibly be embedded within other views (within other views, etc., etc.) if you need this type of hierarchy with <strong>ViewExtends</strong>.
                        </div>
                        <div className="col-mx">
                            <strong>Views</strong> are never called directly, they must be loaded by a controller. Remember that in an MVC framework, the Controller acts as the traffic cop, so it is responsible for fetching a particular view. If you have not read the Controllers page you should do so before continuing.
                        </div>
                        <div className="col-mx">
                            Using the example controller you created in the controller page, lets add a view to it.
                        </div>

                        <h3>Creating a View</h3>
                        <div className="col-mx">
                            Use command below to create a View :
                        </div>
                        <pre className="syntax-global">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            The command above take 2 parameters:
                            <ul>
                                <li>View Name</li>
                                <li>Controller Name
                                    <div className="col-mx">
                                        <strong>NOTE:</strong> make sure that the <strong>Controller</strong> name (file) is already exist on your project.
                                    </div>
                                </li>
                            </ul>
                        </div>
                        <div className="col-mx">
                            This command will generate a view at <strong>app/view/v_home.dart</strong>:
                        </div>
                        <pre className="syntax">{this.syntax[1]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterView;