import React, { Component } from "react";

class MainRightProfile extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        var nameClass = this.props.nameClass;
        return <div className={"card-content" + ( nameClass === "profile" ? " active" : "")}>
                    <div className="col-mx center m-b-3">
                        <h3>ABOUT ME</h3>
                    </div>
                    <hr/>
                    <div className="p-x">
                        <div className="col-mx m-t-3 cf svg-icon">
                            <span id="--name"></span>
                            <strong>Name</strong>
                            <div className="right">
                                <span>Dimas Awang Kusuma</span>
                            </div>
                        </div>
                        <div className="col-mx cf svg-icon">
                            <span id="--birth"></span>
                            <strong>Date of birth</strong>
                            <div className="right">
                                <span>July 18th 1995</span>
                            </div>
                        </div>
                        <div className="col-mx cf svg-icon">
                            <span id="--email"></span>
                            <strong>E-Mail</strong>
                            <div className="right">
                                <span>dimas.awang.kusuma@gmail.com</span>
                            </div>
                        </div>
                        <div className="col-mx cf svg-icon">
                            <span id="--address"></span>
                            <strong>Address</strong>
                            <div className="right">
                                <span>Pekalongan, Central Java, Indonesia</span>
                            </div>
                        </div>
                    </div>
                    <hr/>
                    <div className="col-mx center m-b-3">
                        <h3>EDUCATION</h3>
                    </div>
                    <div className="card-info">
                        <div className="col-mx">
                            <strong>STMIK WIDYA PRATAMA PEKALONGAN</strong>
                        </div>
                        <div className="col-mx">
                            <i>Computer Science</i>
                        </div>
                        <div className="col-mx">
                            <i>2013-2017</i>
                        </div>
                    </div>
                </div>;
    }
}

export default MainRightProfile;