import React from 'react';
import photo from './../../../../assets/documentations/alert-notifier.gif';

class ArjunaneFlutterAlertNotifier extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`var alert = AlertsDialogNotifier();
alert.show(context);

setTimeout( () { 
    // update status AlertsDialogNotifier after 5 seconds
    alert.updateAlertsDialog(
        context, 
        message: "Data berhasil di simpan", 
        status: AlertsDialogStatus.success);
}, 5000);`,

        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "alert-notifier" ? "block" : "none"}}>
                    <h1>Alerts Dialog Notifier class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>AlertsDialogNotifier</strong> class is an "Alert" widget which can be changed periodically</span>
                        </div>
                        
                        <h3>AlertsDialogNotifier</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            <h3>Method : show</h3>
                            <div className="col-mx">
                                The method to show <strong>AlertsDialogNotifier</strong>.
                            </div>
                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>dismissible</strong> &rarr; <span className="fc-b">bool</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">false</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onClickError</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        Callback when status change to <strong>error</strong>.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onClickSuccess</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        Callback when status change to <strong>success</strong>.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>textButtonError</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text for button text of <strong>onClickError</strong>.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>textButtonSuccess</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text for button text of <strong>onClickSuccess</strong>.
                                    </div>
                                </li>                                
                            </ul>

                            <h3>Method : updateAlertsDialog</h3>
                            <div className="col-mx">
                                The method to update or change the <strong>AlertsDialogNotifier</strong>.
                            </div>
                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>message</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Loading ...</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>status</strong> &rarr; <span className="fc-b">AlertsDialogStatus</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">AlertsDialogStatus.progress</i>
                                    </div>
                                </li>                             
                            </ul>

                            <h3>AlertsDialogStatus enum</h3>
                            <h4>constant</h4>
                            <ul>
                                <li>progress</li>
                                <li>success</li>
                                <li>error</li>
                            </ul>

                            <h3>Output:</h3>
                            <div className="img col-md-6">
                                <img src={photo} alt="Arjunane Alert Notifier"/>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterAlertNotifier;