import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import helper from '../../helper/helper';
import modelDocumentation from '../../model/ModelDocumentation';

class ArjunaneLeft extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activeClass : this.props.uri
        }
        this.unlisten = this.props.history.listen((location, action) => {
            this.setState({activeClass: helper.uriHashSegment(2)});
        });
    }

    componentWillUnmount() {
        this.unlisten();
    }

    changePage(e, page) {
        e.preventDefault();
        var url = e.target.getAttribute("href");
        this.props.history.push({
            pathname: helper.getUrlPath(url)
        });
        this.setState({activeClass: page});
        if(window.innerWidth < 768) document.getElementsByTagName("nav")[0].classList.add("inactive");
        
        this.props.navClick(page);
    }

    setNavigations() {
        this.html = new Array();
        modelDocumentation.getNavigations()[this.props.category].forEach( (val) => {
            if(val.children) {
                this.html.push(<React.Fragment>
                    <strong>{val.title}</strong>
                    <div style={{paddingLeft: 10 + "px"}}>
                        {val.children.map(v => {
                            return <a className={this.state.activeClass === v.url || (v.url === "" && this.state.activeClass === null) ? "active" : ""} onClick={(e) => this.changePage(e, v.url)} href={helper.setUrl("documentation/" + this.props.category + "/" + v.url)}>{v.title}</a>
                        })}
                    </div>
                    
                </React.Fragment>);
                return;
            }
            
            this.html.push(<a className={this.state.activeClass === val.url || (val.url === "" && this.state.activeClass === null) ? "active" : ""} onClick={(e) => this.changePage(e, val.url)} href={helper.setUrl("documentation/" + this.props.category + "/" + val.url)}>{val.title}</a>)
        });
        return this.html;
    }

    render() {
        return <Fragment>
                    <nav>
                            <div class="nav-container">
                                {this.setNavigations()}
                            </div>
                        </nav>
                        <div className="humberger">
                        <svg enable-background="new 0 0 24 24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><g><path d="m21.5 24h-19c-1.379 0-2.5-1.122-2.5-2.5v-19c0-1.378 1.121-2.5 2.5-2.5h19c1.379 0 2.5 1.122 2.5 2.5v19c0 1.378-1.121 2.5-2.5 2.5zm-19-23c-.827 0-1.5.673-1.5 1.5v19c0 .827.673 1.5 1.5 1.5h19c.827 0 1.5-.673 1.5-1.5v-19c0-.827-.673-1.5-1.5-1.5z"/></g><g><path d="m16.5 8h-9c-.276 0-.5-.224-.5-.5s.224-.5.5-.5h9c.276 0 .5.224.5.5s-.224.5-.5.5z"/></g><g><path d="m16.5 12.5h-9c-.276 0-.5-.224-.5-.5s.224-.5.5-.5h9c.276 0 .5.224.5.5s-.224.5-.5.5z"/></g><g><path d="m16.5 17h-9c-.276 0-.5-.224-.5-.5s.224-.5.5-.5h9c.276 0 .5.224.5.5s-.224.5-.5.5z"/></g></svg>
                    </div>
                </Fragment>;
    }
}

export default withRouter(ArjunaneLeft);