import React from 'react';

class ArjunaneXamarinValidationInsertPicker extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`list.Clear();
list.Add("Pekalongan");
list.Add("Kajen");

valid.InsertFormPickerList("location", list);`,
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "insert-picker" ? "block" : "none"}}>
                   <h3>Insert Form List Picker</h3>
                    <div className="col-mx">
                        <span>This method will insert (adding) the value of the <strong>Picker</strong> that you have been set previously.</span>
                    </div>
                    <pre className="syntax">{this.syntax[0]}</pre>
        
                    <div className="col-mx">
                        <span>The above method takes two parameters:</span>
                        <ul>
                            <li>The field name or key that you've set in <strong>SetRules</strong>.</li>
                            <li>Your list that you've adding before.</li>
                        </ul>
                    </div>
                </div>;
    }
}

export default ArjunaneXamarinValidationInsertPicker;