import React from 'react';
import {Route, Switch} from 'react-router-dom';
import helper from '../../../helper/helper';
import ArjunaneLeft from './../container_left';
import ArjunaneTableV1DocsHome from './docs/at_1_home';
import ArjunaneTableV1DocsSimpleInit from './docs/at_2_simple_init';
import ArjunaneTableV1DocsHideOrder from './docs/at_3_hide_order';
import ArjunaneTableV1DocsSearch from './docs/at_4_search';
import ArjunaneTableV1DocsArrayInfo from './docs/at_5_array_info';
import ArjunaneTableV1DocsButtonsClass from './docs/at_6_buttons_class';
import ArjunaneTableV1DocsAction from './docs/at_7_action';
import ArjunaneTableV1DocsCheckableAction from './docs/at_8_checkable_action';
import ArjunaneTableV1DocsPreview from './docs/at_9_preview';

class ArjunaneTableV1Container extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'javascript');
        });

        helper.appendCSS(process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/datatables.min.css");
        helper.appendScript(process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/jquery.js", () => {
            helper.appendScript(process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/datatables.js", () => {
                
                var dt  = window.$("#dt-1"),
                title   = window.$("#title-preview");
                console.log(dt.table)
                dt.table({
                    //url : "Scripts/",
                    json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                    thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                });
                title.html("Tutorials : 1. Simple Init");
    
                window.$("#p-1").on("click", function () {
    
                    dt.table({
                        json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                        thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                    });
                    title.html("Tutorials : 1. Simple Init");
                    dt.focus();
                });
    
                window.$("#p-2").on("click", function () {
    
                    dt.table({
                        json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                        thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                        hide_index: [0],
                        order_by: [2, "desc"]
                    });
                    title.html("Tutorials : 2. Hidden Index and Order By");
                    dt.focus();
                });
    
                window.$("#p-3").on("click", function () {
    
                    dt.table({
                        json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                        thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                        search : 
                        [
                            { index: 0, type: "input", placeholder: "Search ID..."}, // <i>If you hide this index, this is will not show at all.</i>
                            { index: 1, type: "input", placeholder: "Search Name..."},
                            { index: 2, type: "input", placeholder: "Search Grade..."},
                            { index: 3, type: "select", value: [
                                                            ["", "All"], 
                                                            ["1", "Pass"],
                                                            ["0", "Not Pass"],
                                                        ]
                            }
                        ]
                    });
                    title.html("Tutorials : 3. Search");
                    dt.focus();
                });
    
                window.$("#p-4").on("click", function () {
    
                    dt.table({
                        json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                        thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                        array_info: [
                            { index: 3, value: { 
                                    "0" : '<span style="color:red">Not Pass</span>',
                                    "1" : '<span style="color:green">Pass</span>'
                                }
                            }
                        ]
                    });
                    title.html("Tutorials : 4. Array Info");
                    dt.focus();
                });
                
                window.$("#p-6").on("click", function () {
    
                    dt.table({
                        json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                        thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                        action :
                        [
                            {   
                                cls     : "edit",
                                url     : "edit/",
                                text    : "_edit_",
                                params  : [0, 2]
                            },
                            {
                                cls     : "delete",
                                url     : "Scripts/delete",
                                text    : "_hapus_",
                                isRemove: true,
                                params  : [0],
                                field   : 0,
                                msg     : "Do you really want to delete this {i} : {m}?"
                            },
                            {
                                cls     : "change",
                                url     : "Scripts/change",
                                text    : "_change_",
                                params  : [0],
                                changeData: 
                                {
                                    changeIndex : [1, 2] // merubah data text pada index td
                                }
                            }
                        ]
                    });
                    title.html("Tutorials : 6. Actions");
                    dt.focus();
                });
    
                
                window.$("#p-7").on("click", function () {
    
                    dt.table({
                        json: process.env.PUBLIC_URL + "/plugins/my-datatables/Scripts/json.json",
                        thead: ["ID", "Name", "Grade", "Status", "Gender", "Birth", "Place"],
                        checkable: true,
                        action_checkable: 
                        [
                            {
                                cls     : "add",
                                url     : "add/",
                                text    : "TAMBAH BERITA",
                            },
                            {
                                cls     : "delete",
                                url     : "Scripts/delete.php",
                                text    : "HAPUS",
                                params  : [0],
                                isRemove: true,
                                field   : 0,
                                msg     : "Do you really want to delete all of these?",
                                col_msg : "{i} = {m}"
                            },
                            {
                                cls     : "change",
                                url     : "Scripts/change.php",
                                text    : "-CHANGE-",
                                params  : [0],
                                field   : 0,
                                changeData: 
                                {
                                    changeIndex : [1, 2] // merubah data text pada index td
                                },
                                msg     : "Do you really want to change all of these?",
                                col_msg : "{i} = {m}"
                            }
                        ]
                    });
                    title.html("Tutorials : 7. Checkable Actions");
                    dt.focus();
                });
                
            });
        });
        
        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-table-v1" navClick={this.navigationsClick}/>
                    
                    <ArjunaneTableV1DocsHome uri={this.state.uri}/>
                    <ArjunaneTableV1DocsSimpleInit uri={this.state.uri}/>
                    <ArjunaneTableV1DocsHideOrder uri={this.state.uri}/>
                    <ArjunaneTableV1DocsSearch uri={this.state.uri}/>
                    <ArjunaneTableV1DocsArrayInfo uri={this.state.uri}/>
                    <ArjunaneTableV1DocsButtonsClass uri={this.state.uri}/>
                    <ArjunaneTableV1DocsAction uri={this.state.uri}/>
                    <ArjunaneTableV1DocsCheckableAction uri={this.state.uri}/>
                    <ArjunaneTableV1DocsPreview uri={this.state.uri}/>

                </React.Fragment>;
    }
}

export default ArjunaneTableV1Container;