import React from 'react';
import photo from './../../../../assets/documentations/buttons1.jpg';
import photo2 from './../../../../assets/documentations/buttons2.jpg';

class ArjunaneFlutterButton extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`Buttons( "Oke", 
    style: TypeStyle.danger,
    onPressed: () {
        // your action
    }
)`,

`Buttons(
    String text, {
        Key key,
        Function onPressed, Function onLongPressed, 
        TypeStyle style = TypeStyle.primary, 
        IconData icon, 
        bool isDisabled = false
    }
)`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "button" ? "block" : "none"}}>
                    <h1>Button</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>button</strong> method defines a clickable button.</span>
                        </div>
                        <h3>button</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>text</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The text to display
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>icon</strong> &rarr; <span className="fc-b">Widget</span>
                                    </div>
                                    <div className="col-mx">
                                        The icon to display inside the button.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>key</strong> &rarr; <span className="fc-b">Key</span>
                                    </div>
                                    <div className="col-mx">
                                        Controls how one widget replaces another widget in the tree.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onPressed</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is tapped or otherwise activated.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onLongPressed</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is long-pressed.
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>isDisabled</strong> &rarr; <span className="fc-b">bool</span>
                                    </div>
                                    <div className="col-mx">
                                        Whether the button is enabled or disabled.
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">false</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>style</strong> &rarr; <span className="fc-b">TypeStyle</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">TypeStyle.primary</i>
                                    </div>
                                </li>
                            </ul>

                            <h3>Output:</h3>
                            <div className="cf">
                                <div className="img col-md-6 left">
                                    <img src={photo} alt="Arjunane Buttons"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photo2} alt="Arjunane Buttons Icons"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterButton;