import React from 'react';

class ArjunaneUnityClick extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`ar.Get("Buttons").Click( () => {
    // your action
    int index = ar.Index;
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "click" ? "block" : "none"}}>
                    <h1>Click</h1>
                    <div className="col-mx">
                        <div className="col-mx">
                            <span>Give listener to your Button(s)</span>
                        </div>
                        <div className="col-mx">
                            <div className="bg-w2 p-x">
                                <pre className="syntax">{this.syntax[0]}</pre>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityClick;