import React from 'react';

class ArjunaneTableV2DocsReq extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`arTable.req("your-url", function (res) {
    if(res.error !== null)
    {
        console.log(res.result);
        // success
    }
    else
    {
        console.log(res.error.status);
        console.log(res.error.statusText);
        // error
    }
});`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "req" ? "block" : "none"}}>
                   <h1>Req</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>req</strong> method is used to request to your server
                        </div>
                        <div className="col-mx">
                            <span>It takes 2 parameters :</span>
                            <ul>
                                <li>url : string</li>
                                <li>callback : method<br/>
                                    This method takes 2 keys:
                                    <ul>
                                        <li>error
                                            <ul>
                                                <li>status</li>
                                                <li>statusText</li>
                                            </ul>
                                        </li>
                                        <li>result</li>
                                    </ul>
                                </li>
                            </ul>
                        </div>

                        <h3>Example:</h3>
                        <div className="bg-b col-mx">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsReq;