import React from 'react';

class ArjunaneTableV2DocsInsertData extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
            `var setArTable = arTable.init("#table-insert", {
    ...
});
    
document.getElementById("insert").addEventListener("click", function () { 
    var insert = [ 101, "Arjuna", 70, 1, "Male", "2020-05-10", "Pekajangan"];
    setArTable.insertData(insert);
});`,
            `var setArTable = arTable.init("#table-insert", {
    ...
});

document.getElementById("insert").addEventListener("click", function () { 
    var insert = {
        "id"    : 101,
        "name"  : "Arjunane",
        "grade" : 70,
        "passing": 1,
        "birth" : "2020-05-10",
        "place" : "Pekajangan"
    };
    setArTable.insertData(insert);
});`,
            `var setArTable = arTable.init("#table-insert", {
                url     : "json.json",
                title   : "Data Orang",
                thead   : ["ID", "Name", "Grade", "Passing", "Sex", "Birth", "Place"],
                content_top :
                { 
                    action : 
                    [
                        {
                            text    : "Insert Once",
                            type    : "success",
                            href    : "json/insert.json",
                            listener: function (data, event, element)
                            {
                                event.preventDefault();
                                arTable.req(this.href, function (res) {
                                    if(res.error === null && arTable.isJSON(res.result))
                                    {
                                        // it will insert : [101,"Si Cantik",70,1,"Female","2020-04-18","Kedungwuni"]
                                        setArTable.insertData(JSON.parse(res.result));
                                    }
                                    else
                                    {
                                        // Error
                                    }
                                });
                            }
                        },
                        {
                            text    : "Insert Multiple",
                            type    : "info",
                            href    : "json/insert-multiple.json",
                            listener: function  (data, event, element)
                            {
                                event.preventDefault();
                                arTable.req(this.href, function (res) {
                                    if(res.error === null && arTable.isJSON(res.result))
                                    {
                                        /*
                                            it will insert :[
                                                [30,"ter tamVan",60,0,"Male","2020-03-20","Pekalongan"],
                                                [31,"tamVan dan Berani",20,0,"Female","2020-03-19","Pekalongan"]
                                            ]
                                        */
                                        setArTable.insertData(JSON.parse(res.result));
                                    }
                                    else
                                    {
                                        // Error
                                    }
                                });
                            }
                        }
                    ]
                }
            });`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "insert-data" ? "block" : "none"}}>
                    <h1>Insert Data</h1>
                    <div class="p-x">
                        <div class="col-mx">
                            Method of <strong>insertData</strong> is for inserting data to table's internal caches of data.
                        </div>
                        <div class="col-mx">
                            <h3 class="dis-ib">NOTE:</h3> Please to always be consistent in using JSON, either <strong>Array</strong> or <strong>Array of Object</strong> and <strong>length</strong> of array is same with your data before
                        </div>

                        <h3>Insert Array</h3>
                        <div class="col-mx">
                            <span>If your table's internal caches of data is <strong>Array</strong> (also from <strong>init</strong> method to replace your table tag into Arjunane Table)</span>
                        </div>
                        <div class="col-mx">
                            <strong>Example:</strong>
                        </div>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>

                        <h3>Insert Array of Object</h3>
                        <div class="col-mx">
                            <span>If your table's internal caches of data is <strong>Array of Object</strong></span>
                        </div>
                        <div class="col-mx">
                            <strong>Example:</strong>
                        </div>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>

                        <h3>Example inserting</h3>
                        <div class="bg-b m-b-1">
                            <pre className="syntax">{this.syntax[2]}</pre>
                        </div>
                        <h3>Output:</h3>
                        <div id="table-insert"></div>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV2DocsInsertData;