import React from 'react';
import {Route, Switch} from 'react-router-dom';
import './../../../assets/css/documentation_xamarin.min.css';
import helper from '../../../helper/helper';
import ArjunaneLeft from './../container_left';
import SharinganHome from './docs/s_1_home';

class ArjunaneSharingan extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'javascript');
        });
        
        helper.appendCSS(process.env.PUBLIC_URL + "/plugins/sharingan/sharingan.min.css");
        helper.appendScript(process.env.PUBLIC_URL + "/plugins/sharingan/sharingan.min.js", () => {
            window.sharingan(".mencoba").ex({
                type:1,
                size:100
            });

            window.sharingan(".mencoba2").ex({
                type:2,
                size:100
            });
            window.sharingan(".mencoba3").ex({
                type:3,
                size:100
            });
            window.sharingan(".mencoba4").ex({
                type:4,
                size:100
            });
            window.sharingan(".mencoba5").ex({
                type:5,
                size:100
            });
            window.sharingan(".mencoba6").ex({
                type:6,
                size:100
            });
            window.sharingan(".mencoba7").ex({
                type:7,
                size:100
            });
            window.sharingan(".mencoba8").ex({
                type:8,
                size:100
            });
            window.sharingan(".mencoba9").ex({
                type:9,
                size:100
            });
            window.sharingan(".mencoba10").ex({
                type:10,
                size:100
            });
            window.sharingan(".mencoba11").ex({
                type:11,
                size:100
            });
            window.sharingan(".mencoba12").ex({
                type:12,
                size:100
            });
            window.sharingan(".mencoba13").ex({
                type:13,
                size:100
            });
            window.sharingan(".mencoba14").ex({
                type:14,
                size:100
            });
            window.sharingan(".mencoba15").ex({
                type:15,
                size:100
            });
            window.sharingan(".mencoba-transform").ex({
                type:1,
                size:300,
                transform:true,
                typeTransform: 2
            });
            window.sharingan(".mencoba-transform2").ex({
                type:1,
                size:500,
                transform:true,
                typeTransform: 3
            });
            window.sharingan(".mencoba-transform3").ex({
                type:1,
                size:700,
                transform:true,
                typeTransform: 15,
                title: true
            });
        });

        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-sharingan" navClick={this.navigationsClick}/>
                    
                    <SharinganHome uri={this.state.uri}/>
                </React.Fragment>;
    }
}

export default ArjunaneSharingan;