import React from 'react';

import photoDanger from './../../../../assets/documentations/confirmDanger.jpg';
import photoInfo from './../../../../assets/documentations/confirmInfo.jpg';
import photoSuccess from './../../../../assets/documentations/confirmSuccess.jpg';
import photoWarning from './../../../../assets/documentations/confirmWarning.jpg';

class ArjunaneFlutterAlertsConfirm extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`bool resultConfirm = false;
await showConfirmDialog(
    context     : context, 
    message     : "Are you sure want to delete this data?", 
    yesConfirm  : () {
        resultConfirm = true;
        SnackBar(content: Text("Oke button is pressed"));
    }, 
    noConfirm   : () {
        SnackBar(content: Text("No button is pressed"));
    },  
    dismissible : false, 
    textNo      : "NO/TIDAK", 
    textYes     : "YES/YA", 
    title       : "Just Info", 
    typeAlertDialog: TypeAlertDialog.danger
);

if(resultConfirm) {
    // resultConfirm is true
}
else {
    // resultConfirm is false
}`,

`showConfirmDialog({
    @required BuildContext context, @required String message, @required Function yesConfirm, 
    String title = "Info", String textYes = "Yes", String textNo = "No", 
    Function noConfirm, bool dismissible = true,
    TypeAlertDialog typeAlertDialog = TypeAlertDialog.info
})`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "alerts-confirm" ? "block" : "none"}}>
                    <h1>Alerts</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            <span>The <strong>showAlertDialog</strong> method displays a dialog box with a specified message, along with an OK and a Cancel button.</span>
                        </div>
                        <div className="col-mx">
                            <span>A confirm box is often used if you want the user to verify or accept something.</span>
                        </div>
                        <h3>showConfirmDialog</h3>
                        <pre className="syntax">{this.syntax[0]}</pre>
                        <div className="col-mx">
                            
                            <h4>Constructor</h4>
                            <pre className="syntax">{this.syntax[1]}</pre>

                            <h4>Properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>context</strong> &rarr; <span className="fc-b">BuildContext</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        A handle to the location of a widget in the widget tree 
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>dismissible</strong> &rarr; <span className="fc-b">bool</span>
                                    </div>
                                    <div className="col-mx">
                                        If it set to <strong>false</strong>, this means that the user must tap a button to exit the Alert Dialog
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">true</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>message</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The text to display
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>noConfirm</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is tapped or otherwise activated
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>onOk</strong> &rarr; <span className="fc-b">Function</span>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is tapped or otherwise activated
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>textNo</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display button text
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">No</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>textYes</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The text to display button text
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">Yes</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>title</strong> &rarr; <span className="fc-b">String</span>
                                    </div>
                                    <div className="col-mx">
                                        The title to display
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">INFO</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>typeAlertDialog</strong> &rarr; <span className="fc-b">TypeAlertDialog</span>
                                    </div>
                                    <div className="col-mx">
                                        &rarr;<i className="fc-b4 size-12">TypeAlertDialog.info</i>
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>yesConfirm</strong> &rarr; <span className="fc-b">Function</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        The callback that is called when the button is tapped or otherwise activated
                                    </div>
                                </li>
                            </ul>

                            <h3>Output:</h3>
                            <div className="cf">
                                <div className="img col-md-6 left">
                                    <img src={photoDanger} alt="Arjunane Alert Confirm Danger"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photoInfo} alt="Arjunane Alert Confirm Info"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photoSuccess} alt="Arjunane Alert Confirm Success"/>
                                </div>
                                <div className="img col-md-6 left">
                                    <img src={photoWarning} alt="Arjunane Alert Confirm Warning"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterAlertsConfirm;