import React from 'react';

class ArjunaneFlutterHelper extends React.Component {

    constructor(props) {
        super(props);
        var date = new Date();
        var dateNow = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getMilliseconds()}`
        this.syntax = [
`Color redHex = Helper.fromHex("#e74c3c");

Container(
    color: redHex
);`,

`var date = Helper.dateNow();

print("date: $date");
// date: ${dateNow}`,

`String rup = Helper.rupiah(
    "90000", 
    separator: ",", 
    trailing: ",-"
);

Text(rup);
// output : Rp. 90,000,-`,

`String date = Helper.indoDate(
    "2021-03-24 10:00:00", 
    isDayName: true, 
    separator: " "
);

Text(date);
// output : Rabu, 24 Maret 2021`,

`setTimeout( () {
    // your action after 5 seconds
}, 5000)`,

`setInterval( () {
    // your action every 5 seconds
}, 5000)`,

`setInterval( () {
    // your action every 5 seconds
}, 5000, key: "mencoba-interval");

stopInterval(key: "mencoba-interval");`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "helper" ? "block" : "none"}}>

                    <h1>From Hex</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>fromHex</strong> is an immutable 32 bit color value in ARGB format.
                        </div>
                        <div className="col-mx">
                            <h3>fromHex</h3>
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>

                    <h1>Date Now</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>dateNow</strong> is an instant in time, such as 2021-03-22 10:30:00.
                        </div>
                        <div className="col-mx">
                            <h3>dateNow</h3>
                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>
                    </div>

                    <h1>Rupiah</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>rupiah</strong> is to change value into rupiah.
                        </div>
                        <div className="col-mx">
                            <h3>rupiah</h3>
                            <pre className="syntax">{this.syntax[2]}</pre>
                        </div>
                    </div>

                    <h1>Indo Date</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>indoDate</strong> is the date in Indonesia format.
                        </div>
                        <div className="col-mx">
                            <h3>indoDate</h3>
                            <pre className="syntax">{this.syntax[3]}</pre>
                        </div>
                    </div>

                    <h1>setTimeout</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>setTimeout()</strong> method calls a function or evaluates an expression after a specified number of milliseconds.
                        </div>
                        <div className="col-mx">
                            <h3>setTimeout</h3>
                            <pre className="syntax">{this.syntax[4]}</pre>
                        </div>
                    </div>

                    <h1>setInterval</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>setInterval()</strong> method calls a function or evaluates an expression at specified intervals (in milliseconds).
                        </div>
                        <div className="col-mx">
                            <h3>setInterval</h3>
                            <pre className="syntax">{this.syntax[5]}</pre>
                        </div>
                    </div>

                    <h1>stopInterval</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>stopInterval()</strong> method clears a timer set with the setInterval() method.
                        </div>
                        <div className="col-mx">
                            <h3>stopInterval</h3>
                            <pre className="syntax">{this.syntax[6]}</pre>
                        </div>
                    </div>


                </div>;
    }
}

export default ArjunaneFlutterHelper;