import React from 'react';

class ArjunaneTableV1DocsHideOrder extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`$('#dt-1').table({
    json: "Scripts/json.json",
    thead: ["ID", "Name", "Grade", "Gender", "Birth", "Place"],
    hide_index: [0] // You can hide multiple index [0,1,2]
    order_by: [2, "desc"]
});`
        ];
    }

    componentDidMount() {
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "hide-order" ? "block" : "none"}}>
                    <h1>Tutorials : 2. Hidden Index and Order By</h1>
                    <div className="col-mx">
                        <ul>
                            <li>index_hidden : [<strong>array...int</strong>] </li>
                            <li>order_by : [<strong>int (index)</strong>, <strong>"desc"</strong> OR <strong>"asc"</strong>] </li>
                        </ul>
                    </div>
                    <div className="col-mx">
                        <span>Let's say that you have 6 <strong>th</strong> for thead</span> ["ID", "Name", "Grade", "Gender", "Birth", "Place"] or 6 array of data and you want to hide for certain index, so<br/>

                        <ul>
                            <li>ID : 0</li>
                            <li>Name : 1</li>
                            <li>Grade : 2</li>
                            <li>Status : 3</li>
                            <li>Gender : 4</li>
                            <li>Birth : 5</li>
                            <li>Place : 6</li>
                        </ul>

                        Just set where you want to hide the index of data.
                        <div className="col-mx">
                            <span>For example, below is to hidden the index of number <strong>0</strong> and then the index of <strong>0</strong> will be hide (the data is still exist)</span><br/>
                            <span>Or if you want to hidden some index, then <strong>hidden_index : [0,1,2] </strong></span>
                        </div>
                        <div className="col-mx">
                            <strong>But, the index where you already set to hidden, still can be access for parameters</strong>
                        </div>
                        <div className="col-mx">
                            <strong>Order By:</strong>
                            <div className="col-mx">
                                <span>Set where you want to "<strong>Order By</strong>" first when you firstly load the data, for example <strong>order_by : [2, "desc"]</strong> where the index of 2 is <strong>"Grade"</strong></span>
                            </div>
                        </div>
                    </div>
                    <div className="col-mx bg-w2 p-x">
                        <pre className="syntax">{this.syntax[0]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneTableV1DocsHideOrder;