import React from 'react';

class ArjunaneUnityHome extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`using ArjunaneLibrary;

public class Main : MonoBehaviour {

// Use this for initialization
Arjunane ar = new Arjunane();`,

`public class Main : MonoBehaviour {

// Use this for initialization
ArjunaneLibrary.Arjunane ar = new ArjunaneLibrary.Arjunane();`
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "" || this.props.uri === null ? "block" : "none"}}>
                    <div className="col-mx center">
                        <h1>-=[ Arjunane.cs DEMO ]=-</h1>
                        <div className="col-mx">
                            <span>Dependency-free C# library for Unity</span>
                        </div>
                    </div>

                    <div className="col-mx">
                        <strong>Required:</strong>
                        <ul>
                            <li>Arjunane.cs</li>
                            <li>.NET Framework 4.0</li>
                        </ul>
                    </div>

                    <h1>Init</h1>
                    <div className="col-mx">
                        <div className="bg-w2 p-x">
                            <pre className="syntax">{this.syntax[0]}</pre>
                        </div>
                    </div>
                    <div className="col-mx">
                        <strong>OR</strong>
                    </div>
                    <div className="col-mx">

                        <div className="bg-w2 p-x">

                            <pre className="syntax">{this.syntax[1]}</pre>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneUnityHome;