import React from 'react';

class MainRightProjectDetail extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            active  : "",
            activeLeft: "",
            activeRight: "",
            activeBottom : "",
            activeTop   : "",

            css: null,
            date: null,
            download: null,
            icon: null,
            photo: null,
            photos: [],
            preview: null,
            short_url: null,
            tag: null,
            text_en: null,
            text_id: null,
            title_en: null,
            title_id: null,
        };

        this.setDetail = this.setDetail.bind(this);
        //this.changeImage = this.changeImage.bind(this);
    }

    setDetail(data)
    {
        try
        {
            if(data !== null)
            {
                this.state.active   = " active";
                this.state.title_en = data.title_en;
                this.state.photo    = data.photo;
                this.state.photos   = data.photos;
                this.state.date     = data.date;
                this.state.text_en  = data.text_en;
                this.state.download = data.download;
                this.state.preview  = data.preview;
                this.state.tag      = data.tag;
                this.state.icon     = "/icons/" + data.icon;
    
                setTimeout( () => {
                    //this.state.activeLeft = " active";
                    this.setState({activeTop : " active"});
                }, 500);

                setTimeout( () => {
                    //this.state.activeLeft = " active";
                    this.setState({activeLeft : " active"});
                }, 1000);
    
                setTimeout( () => {
                    //this.state.activeRight = " active";
                    this.setState({activeRight : " active"});
                }, 1500);
    
                // setTimeout( () => {
                //     this.setState({activeBottom : " active"});
                // }, 1500);
            }
        }
        catch(e)
        {
            console.log(e)
        }
       
    }

    back()
    {
        this.setState({activeRight: ""});
        setTimeout( () => {
            //this.state.activeLeft = " active";
            this.setState({activeLeft : ""});
        }, 500);

        setTimeout( () => {
            //this.state.activeLeft = " active";
            this.setState({activeTop : ""});
        }, 1000);

        setTimeout( () => {
            //this.state.activeRight = " active";
            this.setState({active : ""});

            this.props.onBackPress();
        }, 1500);
    }

    changeImage(src)
    {
        var preview = document.getElementById("preview");
        preview.setAttribute("src", src);
    }

    render()
    {
        return <div className={"cf detail-project" + this.state.active}>
                    <div className={"detail-top left col-md-12 cf center" + this.state.activeTop}>
                        <div className="btn-back left" onClick={this.back.bind(this)}>BACK</div>
                        <div className="icon img dis-ib">
                            <img src={this.state.icon} alt={this.state.title_en}/>
                        </div>
                    </div>
                    <div className={"detail-left left col-md-6 p-x" + this.state.activeLeft}>
                        <div className="img wd-1 hg-40 m-b-2 img-preview ov-h">
                            <img id="preview" className="m-a" src={this.state.photo} alt={this.state.title_en}/>
                        </div>
                        <div className="col-mx">
                            {this.state.photos.map( (val) => {
                                return  <div class="dis-ib img-preview img hg-10 m-r-1" onClick={() => this.changeImage(val)} style={{cursor: "pointer"}}>
                                            <img src={val} alt={this.state.title_en}/>
                                        </div>
                            })}
                        </div>
                    </div>
                    <div className={"detail-right right col-md-6 p-x" + this.state.activeRight}>
                        <h1>{this.state.title_en}</h1>
                        <div className="col-mx">
                            <div className="col-mx">
                                <strong>DATE:</strong>
                            </div>
                            <i>{this.state.date}</i>
                        </div>
                        <div className="col-mx">
                            <div className="col-mx">
                                <strong>LINK:</strong>
                            </div>
                            {
                                this.state.download !== "" ? 
                                    <a href={this.state.download} className="btn t8 m-n blue" target="_blank">Download</a>
                                : <button className="btn t8 m-n blue fs-lt">Download</button>
                            }
                            {
                                this.state.preview !== "" ? 
                                    <a href={this.state.preview} className="btn t8 m-n green" target="_blank">Preview</a>
                                : <button className="btn t8 m-n green fs-lt">Preview</button>
                            }
                        </div>
                        <div className="col-mx">
                            <div className="col-mx">
                                <strong>INFO</strong>
                            </div>
                            <pre dangerouslySetInnerHTML={{ __html: this.state.text_en }}/>
                        </div>
                        <div className="col-mx">
                            <div className="col-mx">
                                <strong>TAG:</strong>
                            </div>
                            {this.state.tag !== null ? 
                                this.state.tag.split(",").map( (val) => {
                                    
                                    return <div className="btn t3 p-v blue">{val}</div>
                                })
                            : ""}
                        </div>
                    </div>
                </div>;
    }
}

export default MainRightProjectDetail;