import React from 'react';

class ArjunaneFlutterCommands extends React.Component {

    constructor(props) {
        super(props);

        this.syntax = [
`dart arjunane create:command mencoba`,

`import '../../system/command/commands_class.dart';

class MencobaCommand extends Command {

  // The name and signature of the console command.
  String signature = "make:mencoba {user}";

  // The console command description.
  String description = "Create a Command";

  @override
  void handle() {
    // TODO: implement handle
    print("ASIYAP");
    super.handle();
  }

}`,

`// The name and signature of the console command.
String signature = "make:mencoba {user}";`,

`// Optional argument...
String signature = "make:mencoba {user?}"

// Optional argument with default value...
String signature = "make:mencoba {user=tamvan}"`,

`// The name and signature of the console command.
String signature = "make:mencoba {user} {--opsi}";`,

`dart arjunane make:mencoba tamvan --opsi`,

`// The name and signature of the console command.
String signature = "make:mencoba {user} {--opsi=}";`,

`dart arjunane make:mencoba tamvan --opsi=berani`,

`// The name and signature of the console command.
String signature = "make:mencoba {user} {--opsi=asiyap}";`,

`// The name and signature of the console command.
String signature = "make:mencoba {user} {--opsi?}";`,

`make:mencoba {user} {--opsi=*}

dart arjunane make:mencoba tamvan --opsi=1 --opsi=2`,

`import '../../system/command/commands_class.dart';

class MencobaCommand extends Command {

  @override
  void handle() {

    var user = arguments['user'];

    super.handle();
  }

}`,

`var users = arguments;`,

`// Retrieve a specific option...
var option = options['opsi'];

// Retrieve all options as an array Map...
var options = options;`,


`import '../../system/command/commands_class.dart';

class MencobaCommand extends Command {

  @override
  void handle() {

    var userName = ask("What's your name?");

    super.handle();
  }

}`,

`import '../../system/command/commands_class.dart';

class MencobaCommand extends Command {

  @override
  void handle() {

    var userPassword = secret("What's your password?");

    super.handle();
  }

}`,

`import '../../system/command/commands_class.dart';

class MencobaCommand extends Command {

  @override
  void handle() {

    if (confirm('Do you wish to continue?')) {
        //
    }

    super.handle();
  }

}`,
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "commands" ? "block" : "none"}}>
                    <h1>Commands class</h1>
                    <div className="p-x">
                        <h3>Generating Commands</h3>
                        <div className="col-mx">
                            To create a new command, you may use the <span className="fc-r">create:command</span> Arjunane command. This command will create a new command class in the app/Commands directory. Don't worry if this directory does not exist in your application - it will be created the first time you run the <span className="fc-r">create:command</span> Arjunane command:
                        </div>
                        <div className="col-mx">
                            For example:
                            <pre className="syntax-global">{this.syntax[0]}</pre>
                            The output of file will : <strong>mencoba_controller.dart</strong>
                        </div>

                        <h3>Command Structure</h3>
                        <div className="col-mx">
                            After generating your command, you should define appropriate values for the <span className="fc-r">signature</span> and <span className="fc-r">description</span> properties of the class. These properties will be used when displaying your command on the list screen. The <span className="fc-r">signature</span> property also allows you to define your command's input expectations. The <span className="fc-r">handle</span> method will be called when your command is executed. You may place your command logic in this method.
                        </div>
                        <div className="col-mx">
                            Let's take a look at an example command:
                        </div>
                        <pre className="syntax">{this.syntax[1]}</pre>

                        <h3>Arguments</h3>
                        <div className="col-mx">
                            All user supplied arguments and options are wrapped in curly braces. In the following example, the command defines one required argument: <span className="fc-r">user:</span>
                        </div>
                        <pre className="syntax">{this.syntax[2]}</pre>
                        <div className="col-mx">
                            You may also make arguments optional or define default values for arguments:
                        </div>
                        <pre className="syntax">{this.syntax[3]}</pre>

                        <h3>Options</h3>
                        <div className="col-mx">
                            Options, like arguments, are another form of user input. Options are prefixed by two hyphens (--) when they are provided via the command line. There are two types of options: those that receive a value and those that don't. Options that don't receive a value serve as a boolean "switch". Let's take a look at an example of this type of option:
                        </div>
                        <pre className="syntax">{this.syntax[4]}</pre>
                        <div className="col-mx">
                            In this example, the <span className="fc-r">--opsi</span> switch may be specified when calling the Arjunane command. If the <span className="fc-r">--opsi</span> switch is passed, the value of the option will be <span className="fc-r">true</span>. Otherwise, the value will be <span className="fc-r">false</span>:
                        </div>
                        <pre className="syntax">{this.syntax[5]}</pre>

                        <h3>Options with Values</h3>
                        <div className="col-mx">
                            Next, let's take a look at an option that expects a value. If the user must specify a value for an option, you should suffix the option name with a <span className="fc-r">=</span> sign:
                        </div>
                        <pre className="syntax">{this.syntax[6]}</pre>
                        <div className="col">
                            In this example, the user may pass a value for the option like so. If the option is not specified when invoking the command, its value will be <span className="fc-r">null</span>:
                        </div>
                        <pre className="syntax">{this.syntax[7]}</pre>
                        <div className="col">
                            You may assign default values to options by specifying the default value after the option name. If no option value is passed by the user, the default value will be used:
                        </div>
                        <pre className="syntax">{this.syntax[8]}</pre>
                        <div className="col-mx">
                            You may also make options optional:
                        </div>
                        <pre className="syntax">{this.syntax[9]}</pre>
                        <div className="col-m">
                            When the user do not pass an option, the option of <span className="fc-r">--opsi</span> will be <span className="fc-r">null</span>.
                        </div>

                        <h3>Options Array</h3>
                        <div className="col-mx">
                            When defining an option that expects multiple input values, each option value passed to the command should be prefixed with the option name:
                        </div>
                        <pre className="syntax">{this.syntax[10]}</pre>

                        <h3>Retrieving Inputs</h3>
                        <div className="col-mx">
                            While your command is executing, you will likely need to access the values for the arguments and options accepted by your command. To do so, you may use the <span className="fc-r">argument</span> and <span className="fc-r">option</span> methods. If an argument or option does not exist, <span className="fc-r">null</span> will be returned:
                        </div>
                        <pre className="syntax">{this.syntax[11]}</pre>
                        <div className="col-mx">
                            If you need to retrieve all of the arguments as an <span className="fc-r">array Map</span>, call the <span className="fc-r">arguments</span> method:
                        </div>
                        <pre className="syntax">{this.syntax[12]}</pre>
                        <div className="col-mx">
                            Options may be retrieved just as easily as arguments using the option method. To retrieve all of the options as an array Map, call the options method:
                        </div>
                        <pre className="syntax">{this.syntax[13]}</pre>

                        <h3>Prompting For Input</h3>
                        <div className="col-mx">
                            In addition to displaying output, you may also ask the user to provide input during the execution of your command. The <span className="fc-r">ask</span> method will prompt the user with the given question, accept their input, and then return the user's input back to your command:
                        </div>
                        <pre className="syntax">{this.syntax[14]}</pre>
                        <div className="col-mx">
                            The <span className="fc-r">secret</span> method is similar to <span className="fc-r">ask</span>, but the user's input will not be visible to them as they type in the console. This method is useful when asking for sensitive information such as passwords:
                        </div>
                        <pre className="syntax">{this.syntax[15]}</pre>

                        <h3>Asking For Confirmation</h3>
                        <div className="col-">
                            If you need to ask the user for a simple "yes or no" confirmation, you may use the <span className="fc-r">confirm</span> method. By default, this method will return <span className="fc-r">false</span>. However, if the user enters <span className="fc-r">Y</span> in response to the prompt, the method will return <span className="fc-r">true</span>.
                        </div>
                        <pre className="syntax">{this.syntax[16]}</pre>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterCommands;